<!--@Author ygy
pager-count属性可以设置最大页码按钮数。
设置layout，表示需要显示的内容，用逗号分隔，
布局元素会依次显示。prev表示上一页，next为下一页，pager表示页码列表，
除此以外还提供了jumper和total，size和特殊的布局符号->，->后的元素会靠右显示，
jumper表示跳页元素，total表示显示页码总数，size用于设置每页显示的页码数量。
-->
<template>
 <el-pagination
   background
   :page-size="pageSize"
   :pager-count="pagerCount"
   :current-page.sync="currentPage"
   layout="total,prev, pager, next"
   @current-change="handleCurrentChange"
   :total="this.modalObj.tableColumn.length">
 </el-pagination>
</template>

<script>
  var linq = require("linq");
   export default {
     watch: { // 监听数据变化
       'modalObj.isShow': function(newVal){
         var _self = this;
         if (newVal) {
           setTimeout(function () {
             _self.handleCurrentChange(1);
           },100);
         }
       },
     },
     name: "",
     props: {
       modalObj: {
         type: Object,
         default: {}
       },
     },
     data:function (){
         return{
           total:"0",
           pageSize:"10",
           pagerCount:"7",
           layout:"total,prev, pager, next",
           arr:[],
           currentPage:"1",
      }
     },
     methods:{
         /**
          * @Author ygy
          * @Description //TODO 前端分页
          * @Date 10:13 2019/8/29
          * @Param
          * @return
          **/
       handleCurrentChange:function(val){  //页码从0 开始，分页从1开始
         this.currentPage = val;
         this.modalObj.paginationTableColumn = this.arr[val-1];
       },
       pageingData:function () {
       var page =  Math.ceil(this.modalObj.tableColumn.length/this.pageSize) // 计算页码
         for(var i=1;i<=page;i++){
           var start,end;
           if(i ==1 || i =="1"){
             start = 0;
             end = i*this.pageSize-1;
           }else{
             end = i*this.pageSize-1;
             start = i*this.pageSize-10;
           }
           this.arr.push(linq.from(this.modalObj.tableColumn).where('v,i=>i>='+start+'&&i<='+end).defaultIfEmpty([]).toArray());
           console.log(this.arr);
         }
       }
     },
     created:function (){
       if(!this.modalObj.total){
         this.total = this.modalObj.tableColumn.length
       }
       if(!this.modalObj.pageSize){
         this.pageSize = this.modalObj.pageSize
       }
       if(!this.modalObj.pagerCount){
         this.pagerCount = this.modalObj.pagerCount
       }
       if(!this.modalObj.layout){
         this.layout = this.modalObj.layout
       }
       this.pageingData();
       this.handleCurrentChange(1);
     },
     mounted: function () {

     }

   }
</script>

<style scoped>

</style>



