<template>
  <el-tabs class="bar1" v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="填表说明" name="first" ><div class="el-tab-pane1"><el-scrollbar style="height: 100%;"><pre class="bar-pre" v-html="instruction"></pre></el-scrollbar></div></el-tab-pane>
    <el-tab-pane label="审核公式" name="second"><div class="el-tab-pane1"><el-scrollbar style="height: 100%;"><pre class="bar-pre" id="ces" ></pre></el-scrollbar></div></el-tab-pane>
  </el-tabs>
</template>

<script>
  import https from '../https.js'
  export default {
    props: ['parentName'],
    data() {
      return {
        activeName: "first", // 默认标签为1
        name: this.parentName, // 报表组件名称
        instruction:"", // 组件内填表说明
        url:"" // 填表说明请求地址
      };
    },
    watch:{
      activeName(val) {
        console.log(val)
      },
    },
    methods: {
      handleClick(tab, event) {
      },
      //定位到错误的input框
      locationErrorInput () {
        alert("1");
      }
    },
    mounted: function () {
      https.get(this.url,{} ).then((data) => {
        console.log("")
        this.instruction = eval(data.data);
      }).catch(err=>{
        }
      )
      this.$EventBus.$on("activeName", ({activeName}) => {
        this.activeName = activeName;
      });
    },
    created:function () {
      this.url=(this.jsonUrl+"/static/json/"+this.name.split("_")[0]+"/"+this.name+".json");
      console.log(this.url)
    },
    computed:function (){
      this.activeName=this.$store.state.activeName;
    },
  };
</script>
<style>
  .bar1{
    top: 90px;
    right:20px;
    padding: 12px 0px 0px 12px;
    border-radius: 4px;
    background-color: #E4E7ED;
  }
  .bar{
    z-index: 100;
  }
  .el-scrollbar__wrap{
    overflow: scroll;
    width: 110% !important;
    height: 120% !important;
  }

</style>



