<template>
  <el-container>
    <el-header>
      <app-header :parentData="tableData" :parentValidation="validationValue" :parentPdfName="name"></app-header>
    </el-header>
    <el-container>
      <el-main id="allTable">
        <el-scrollbar style="height: 100%;">
          <div style="padding: 20px ;background-color: #fff" id="pdfDom" name="pdfDomBranch" class="pdfDomBranch">
            <table cellspacing="0" cellpadding="0" width="1224" height="310" id="taxTable">
              <tr>
                <td class="noBorder" width="1120" height="46" align="center" valign="middle" colspan="7"><B>年度航空运输企业年度清算表</B></td>
              </tr>
              <tr>
                <td class="noBorder" width="1120" height="22" align="center" valign="middle" colspan="7">税款所属时间:
                  
                  <span v-format-date v-html="tableData.pageData.sbbheadVO.skssqq"></span>
                  至
                  <span v-format-date v-html="tableData.pageData.sbbheadVO.skssqz"></span>
                </td>
              </tr>
              <tr>
                <td class="noBorder" width="1120" height="22" align="left" valign="middle" colspan="7">纳税人名称（公章）:
                  <span v-html="tableData.pageData.sbbheadVO.hkysqymc"></span>
                </td>
              </tr>
              <tr>
                <td class="noBorder" width="800" height="22" align="left" valign="middle" colspan="5">纳税人识别号:
                  <span v-html="tableData.pageData.sbbheadVO.nsrsbh"></span>
                </td>
                <td class="noBorder" width="320" height="22" align="right" valign="middle" colspan="2">金额单位：元至角分</td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="480" height="22" align="center" valign="middle" colspan="3">总机构汇总的年度销售额</td>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle">1</td>
                <td class="editable-bgColor" width="480" height="22" align="right" valign="middle" colspan="3">
                   <input type="text" class="input-border-bgColor" data-id="zjghzdndxse" @blur="retainValidDecimal('sbbheadVO.zjghzdndxse','2')" v-model="tableData.pageData.sbbheadVO.zjghzdndxse" />
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="480" height="22" align="center" valign="middle" colspan="3">总机构汇总的年度应纳税额</td>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle">2</td>
                <td class="editable-bgColor" width="480" height="22" align="right" valign="middle" colspan="3">
                   <input type="text" class="input-border-bgColor" data-id="zjghzdndynse" @blur="retainValidDecimal('sbbheadVO.zjghzdndynse','2')" v-model="tableData.pageData.sbbheadVO.zjghzdndynse" />
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="160" height="44" align="center" valign="middle" rowspan="2"> 分支机构识别号 </td>
                <td class="uneditable-bgColor" width="160" height="44" align="center" valign="middle" rowspan="2"> 分支机构名称 </td>
                <td class="uneditable-bgColor" width="160" height="44" align="center" valign="middle" rowspan="2"> 所在地 </td>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle"> 年度销售额 </td>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle"> 年度清算的应纳税额 </td>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle"> 年度预缴税额 </td>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle"> 差额 </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle"> 3 </td>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle"> 4=（3÷1）×2 </td>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle"> 5 </td>
                <td class="uneditable-bgColor" width="160" height="22" align="center" valign="middle"> 6=4-5 </td>
              </tr>
              <tr v-for="(item,index) of tableData.pageData.ndhkysqyqsbGrid.ndhkysqyqsbGridlb" :key="index">
                <td class="editable-bgColor" width="160" height="22" align="left" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'fzjgsbh'+index" v-model="item.fzjgsbh" />
                </td>
                <td class="editable-bgColor" width="160" height="22" align="left" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'fzjgmc'+index" v-model="item.fzjgmc" />
                </td>
                <td class="editable-bgColor" width="160" height="22" align="left" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'szd'+index" v-model="item.szd" />
                </td>
                <td class="editable-bgColor" width="160" height="22" align="right" valign="middle">
                  <input class="input-border-bgColor":data-id="'ndxse'+index" v-model="item.ndxse" @blur="retainValidDecimal('sbbheadVO.zjghzdndynse','2')" />
                </td>
                <td class="font-color-red" width="160" height="22" align="right" valign="middle"  :data-id="'ndqsdynse'+index" v-html="item.ndqsdynse"></td>
                <td class="editable-bgColor" width="160" height="22" align="right" valign="middle">
                  <input class="input-border-bgColor"  :data-id="'ndyjse'+index" v-model="item.ndyjse" @blur="retainValidDecimal('sbbheadVO.zjghzdndynse','2')" />
                </td>
                <td class="font-color-red" width="160" height="22" align="right" valign="middle" :data-id="'ce'+index" v-html="item.ce"></td>
                <td width="151" height="22" align="left" valign="middle" class="noBorder">
                  <addBtn :data-name="1" :data-num="index" :parentListData="tableData" :parentListKey="'ndhkysqyqsbGrid.ndhkysqyqsbGridlb'" v-show="index == tableData.pageData.ndhkysqyqsbGrid.ndhkysqyqsbGridlb.length - 1"></addBtn>
                  <deleteBtn :data-name="1" :data-num="index" :parentListData="tableData" :parentListKey="'ndhkysqyqsbGrid.ndhkysqyqsbGridlb'" v-show="index != 0"></deleteBtn>
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="480" height="22" align="center" valign="middle" colspan="3"> 合计 </td>
                <td class="font-color-red" width="160" height="22" align="right" valign="middle" data-id="ndxsehj" v-html="tableData.pageData.otherData.ndhkysqyqsbGridlbhj.ndxsehj"></td>
                <td class="font-color-red" width="160" height="22" align="right" valign="middle" data-id="ndqsdynsehj" v-html="tableData.pageData.otherData.ndhkysqyqsbGridlbhj.ndqsdynsehj"></td>
                <td class="font-color-red" width="160" height="22" align="right" valign="middle" data-id="ndyjsehj" v-html="tableData.pageData.otherData.ndhkysqyqsbGridlbhj.ndyjsehj"></td>
                <td class="font-color-red" width="160" height="22" align="right" valign="middle" data-id="cehj" v-html="tableData.pageData.otherData.ndhkysqyqsbGridlbhj.cehj"></td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="320" height="22" align="center" valign="middle" colspan="2"> 办理人 </td>
                <td width="320" height="22" align="left" valign="middle" colspan="2"> &nbsp; </td>
                <td class="uneditable-bgColor" width="320" height="22" align="center" valign="middle" colspan="2"> 办理人员身份证件类型 </td>
                <td width="160" height="22" align="left" valign="middle"></td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="320" height="22" align="center" valign="middle" colspan="2"> 办理人员身份证件号码 </td>
                <td width="800" height="22" align="left" valign="middle" colspan="5"> &nbsp; </td>
              </tr>
            </table>
          </div>
        </el-scrollbar>
      </el-main>
      <el-aside class="bar">
        <informationBar :parentName="name"></informationBar>
      </el-aside>
    </el-container>
  </el-container>
</template>

<script>
/**
 * @Author lmz
 * @Description 年度航空运输企业年度清算表
 * @Date
 **/
import Header from '../../app-header';
import informationBar from '../../informationBar.vue';
import router from '../../../router/index.js';
import addBtn from '../../addBtn.vue';
import deleteBtn from '../../deleteBtn.vue';
import modal from '../../dialog';
import pagination from '../../pagination';

var linq = require('linq');
export default {
  name: '10106_001_10',
  components: {
    'app-header': Header,
    'informationBar': informationBar,
    'addBtn': addBtn,
    'deleteBtn': deleteBtn,
    'modal': modal,
    pagination
  },
  props: ['routerParam'],
  data: function () {
    return {
      tableData: {
        header: {
          zsxmDm: "10101",// 征收项目代码
          bblxDm: "10106",// 报表类型代码
          bbDm: "10106_001",// 报表代码
          sksssjq: "20190401",// 税款所属时间起
          sksssjz: "20190430",// 税款所属时间止
          saveUserId: "949407",// 保存用户ID
          nsrsbh: "91120102553420400T",// 纳税人识别号 (追加参数)
          djxh: "10111242000025815982",// 登记序号
          pcDm: "0",// 多批次代码（不是多批次填0，是多批次从1开始计算）
          multiPatch: "N",// 按批申报，Y：是；N：不是
          nsqxdm: "06",// 纳税期限代码
          nsrsbq: "201905",// 纳税申报期
          versionNumber: "10" //版本号（报表属于那个版本）
        },
        pageData: {
          // tableData.pageData.sbbheadVO
          sbbheadVO: {
            nsrsbh: "",               //纳税人识别号
            hkysqymc: "",             //航空运输企业名称，纳税人名称
            skssqq: "",               //税款所属期起
            skssqz: "",               //税款所属期止
            zjghzdndxse: "0.00",          //总机构汇总的年度销售额
            zjghzdndynse: "0.00"          //总机构汇总的年度应纳税额
          },
          ndhkysqyqsbGrid: {
            // tableData.pageData.ndhkysqyqsbGrid.ndhkysqyqsbGridlb
            ndhkysqyqsbGridlb: [
              {
                fzjgsbh: "",       //分支机构识别号
                fzjgmc: "",        //分支机构名称
                szd: "",           //所在地
                ndxse: "0.00",         //年度销售额
                ndqsdynse: "0.00",     //年度清算的应纳税额
                ndyjse: "0.00",        //年度预缴税额
                ce: "0.00",            //差额
                hkysqymc: ""       //航空运输企业名称，纳税人名称
              }
            ]
          },
          // 表间数据的校验
          checkData: {
          },
          otherData: {
            // 增加行 备份
            ndhkysqyqsbGridlb: [
              {
                fzjgsbh: "",       //分支机构识别号
                fzjgmc: "",        //分支机构名称
                szd: "",           //所在地
                ndxse: "",         //年度销售额
                ndqsdynse: "",     //年度清算的应纳税额
                ndyjse: "",        //年度预缴税额
                ce: "",            //差额
                hkysqymc: ""       //航空运输企业名称，纳税人名称
              }
            ],
            ndhkysqyqsbGridlbhj:{
              ndxsehj: "0.00",         //年度销售额
              ndqsdynsehj: "0.00",     //年度清算的应纳税额
              ndyjsehj: "0.00",        //年度预缴税额
              cehj: "0.00",            //差额
            }
          }
        }
      },
      certificateDictionary: '', //身份字典
      cityDictionary: '',
      validationValue: [], //校验规则
      requiredItems: [], //必填项目
      name: '10106_001_10', //文件名称
      mbyqndks: '', //弥补以前年度亏损
      initializationData: '', //初始化数据
      saveInfo: '',//已保存过的数据
    };
  },
  computed: {

  },
  watch: {
    // 监听数据变化
  },
  created: function () {
    this.initializationData = this.getSelectSbbInfo(); // 初始化 取值
    this.initializationDataAssignment(); // 初始化表内数据 赋值
    this.inTableVerification(); // 表内校验
    this.betweenTables(); // 表间数据处理
    this.initializationOfPublicServices()  //  暂时替换header节点
  },
  mounted: function () {
    this.keyboardSwitchingCell('taxTable', 'text'); // 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
    //this.getPdf(this.name,true);
  },
  methods: {
    // 初始化表内数据
    initializationDataAssignment: function () {
      this.tableData.pageData.sbbheadVO.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh;//纳税人识别号
      this.tableData.pageData.sbbheadVO.hkysqymc = this.initializationData.init.jbxx[0].nsrmc;//纳税人名称
      this.tableData.pageData.sbbheadVO.skssqq = this.initializationData.data[0].header.sksssjq;//税款所属期起
      this.tableData.pageData.sbbheadVO.skssqz = this.initializationData.data[0].header.sksssjz;//税款所属期止
      this.tableData.pageData.otherData.tbrq = this.initializationData.openTableData.openTableDate  //填表日期
    },
    // 表间数据处理
    betweenTables: function () {

    },
    //初始化表内校验
    inTableVerification: function () {

    },
  },
  provide() {
    //子页面调用父页面方法
    return {
      resetData: this.resetData
    };
  }
};
</script>

<style scoped>
.tdBorder th {
  border: 1px solid #333 !important;
}
.cancelBtn {
  margin-top: 20px;
}
</style>




