<template>
  <el-container>
    <el-header>
      <app-header :parentData="tableData" :parentValidation="validationValue" :parentPdfName="name"></app-header>
    </el-header>
    <el-container>
      <el-main id="allTable">
        <el-scrollbar style="height: 100%;">
          <div style="padding: 20px ;background-color: #fff" id="pdfDom" name="pdfDomBranch" class="pdfDomBranch">
            <table cellspacing="0" cellpadding="0" width="1580" height="178" id="taxTable">
              <tr>
                <td width="1190" height="46" align="center" valign="middle" colspan="8" class="noBorder"><B>航空运输企业总机构接收分支机构传递单</B></td>
              </tr>
              <tr>
                <td width="150" height="22" align="left" valign="middle" colspan="4" class="noBorder">总机构纳税人识别号：<span v-html="tableData.pageData.head.publicHead.nsrsbh"></span></td>
                <td width="480" height="22" align="left" valign="middle" colspan="4" class="noBorder">纳税人名称（公章）：<span v-html="tableData.pageData.head.publicHead.nsrmc"></span></td>
              </tr>
              <tr>
                <td width="1190" height="22" align="center" valign="middle" colspan="8" class="noBorder">税款所属时间：
                  <span v-format-date v-html="tableData.pageData.head.publicHead.sssq.rqQ"></span>
                  至
                  <span v-format-date v-html="tableData.pageData.head.publicHead.sssq.rqZ"></span>
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="150" height="22" align="center" valign="middle">分支机构纳税人识别号</td>
                <td class="uneditable-bgColor" width="400" height="22" align="center" valign="middle">分机构纳税人名称</td>
                <td class="uneditable-bgColor" width="80" height="22" align="center" valign="middle">所属时期起</td>
                <td class="uneditable-bgColor" width="80" height="22" align="center" valign="middle">所属时期止</td>
                <td class="uneditable-bgColor" width="120" height="22" align="center" valign="middle">营业（收入）额</td>
                <td class="uneditable-bgColor" width="120" height="22" align="center" valign="middle">应缴纳增值税</td>
                <td class="uneditable-bgColor" width="120" height="22" align="center" valign="middle">已缴纳增值税</td>
                <td class="uneditable-bgColor" width="120" height="22" align="center" valign="middle">进项税额 </td>
              </tr>
              <tr v-for="(item,index) in tableData.pageData.body.fzjgxx.yjnqk" :key="index">
                <td class="editable-bgColor" width="150" height="22" align="center" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'fzjgNsrsbh'+index" v-model="item.fzjgNsrsbh" />
                </td>
                <td class="editable-bgColor" width="400" height="22" align="center" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'nsrmc'+index" v-model="item.nsrmc" />
                </td>
                <td class="editable-bgColor" width="80" height="22" align="center" valign="middle">
                  <el-date-picker style="width: 140px" :data-id="'rqQ'+index" v-model="item.sssq.rqQ" type="date" value-format="yyyy-MM-dd" placeholder></el-date-picker>
                </td>
                <td class="editable-bgColor" width="80" height="22" align="center" valign="middle">
                  <el-date-picker style="width: 140px" :data-id="'rqZ'+index" v-model="item.sssq.rqZ" type="date" value-format="yyyy-MM-dd" placeholder></el-date-picker>
                </td>
                <td class="editable-bgColor" width="120" height="22" align="right" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'yysre'+index" v-float="2" v-model="item.yysre" />
                </td>
                <td class="editable-bgColor" width="120" height="22" align="right" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'ynzzse'+index" v-float="2" v-model="item.ynzzse" />
                </td>
                <td class="editable-bgColor" width="120" height="22" align="right" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'yjnzzse'+index" v-float="2" v-model="item.yjnzzse" />
                </td>
                <td class="editable-bgColor" width="120" height="22" align="right" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'jxse'+index" v-float="2" v-model="item.jxse" />
                </td>
                <td width="151" height="22" align="left" valign="middle" class="noBorder">
                  <addBtn :data-name="1" :data-num="index" :parentListData="tableData" :parentListKey="'body.fzjgxx.yjnqk'" v-show="index == tableData.pageData.body.fzjgxx.yjnqk.length - 1"></addBtn>
                  <deleteBtn :data-name="1" :data-num="index" :parentListData="tableData" :parentListKey="'body.fzjgxx.yjnqk'" v-show="index != 0"></deleteBtn>
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="150" height="22" align="center" valign="middle">合计</td>
                <td class="uneditable-bgColor" width="400" height="22" align="center" valign="middle">--</td>
                <td class="uneditable-bgColor" width="80" height="22" align="center" valign="middle">--</td>
                <td class="uneditable-bgColor" width="80" height="22" align="center" valign="middle">--</td>
                <td class="font-color-red" width="120" height="22" align="right" valign="middle" v-float="2" v-html="tableData.pageData.otherData.yjnqkhj.yysrehj"></td>
                <td class="font-color-red" width="120" height="22" align="right" valign="middle" v-float="2" v-html="tableData.pageData.otherData.yjnqkhj.ynzzsehj"></td>
                <td class="font-color-red" width="120" height="22" align="right" valign="middle" v-float="2" v-html="tableData.pageData.otherData.yjnqkhj.yjnzzsehj"></td>
                <td class="font-color-red" width="120" height="22" align="right" valign="middle" v-float="2" v-html="tableData.pageData.otherData.yjnqkhj.jxsehj"></td>
              </tr>
            </table>
          </div>
        </el-scrollbar>
      </el-main>
      <el-aside class="bar">
        <informationBar :parentName="name"></informationBar>
      </el-aside>
    </el-container>
  </el-container>
</template>

<script>
/**
 * @Author lmz
 * @Description 航空运输企业总机构接收分支机构传递单
 * @Date
 **/
import Header from '../../app-header';
import informationBar from '../../informationBar.vue';
import router from '../../../router/index.js';
import addBtn from '../../addBtn.vue';
import deleteBtn from '../../deleteBtn.vue';
// import func from '../../../../vue-temp/vue-editor-bridge';

var linq = require('linq');
export default {
  name: '10101_019_10',
  components: {
    'app-header': Header,
    'informationBar': informationBar,
    'addBtn': addBtn,
    'deleteBtn': deleteBtn
  },
  props: ['routerParam'],
  data: function () {
    return {
      certificateDictionary: '', //身份字典
      cityDictionary: '',
      validationValue: [], //校验规则
      requiredItems: [], //必填项目
      name: '10101_019_10', //文件名称
      mbyqndks: '', //弥补以前年度亏损
      initializationData: '', //初始化数据
      saveInfo: '', //已保存过的数据
      tableData: {
        header: {
          zsxmDm:"10101",// 征收项目代码
          bblxDm:"10101",// 报表类型代码
          bbDm:"10101_019",// 报表代码
          sksssjq:"20190401",// 税款所属时间起
          sksssjz:"20190430",// 税款所属时间止
          saveUserId:"949407",// 保存用户ID
          nsrsbh:"91120102553420400T",// 纳税人识别号 (追加参数)
          djxh:"10111242000025815982",// 登记序号
          pcDm:"0",// 多批次代码（不是多批次填0，是多批次从1开始计算）
          multiPatch:"N",// 按批申报，Y：是；N：不是
          nsqxdm:"06",// 纳税期限代码
          nsrsbq:"201905",// 纳税申报期
          versionNumber:"10" //版本号（报表属于那个版本）
        },
        pageData: {
          head: {
            // tableData.pageData.head.publicHead
            publicHead: {
              nsrsbh: null, //纳税人识别号
              nsrmc: null,  //纳税人名称
              sssq: {//sssq.rqQ
                rqQ: null, //日期起
                rqZ: null  //日期止
              },
              tbrq: null    //填表日期
            },
            privateHead: {
              slswjgDm: null,   //受理税务机关代码
              slr: null	  //受理人
            }

          },
          body: {
            fzjgxx: {
              // tableData.pageData.body.fzjgxx.yjnqk
              yjnqk: [{
                fzjgNsrsbh: '', //分支机构纳税人识别号
                nsrmc: '',    //纳税人名称
                sssq: {
                  rqQ: '',   //日期起
                  rqZ: ''    //日期止
                },
                yysre: "0.00",   //营业收入额
                ynzzse: "0.00",   //应缴纳增值税
                yjnzzse: "0.00",  //已缴纳增值税
                jxse:" 0.00",    //进项税额
              }]
            }
          },
          // 表间数据的校验
          checkData: {
            sbqsrq: '2019-07-01',//申报起始日期
            sbjzrq: '2019-09-03'//申报截止日期
          },
          // 显示数据
          otherData: {
            // tableData.pageData.otherData.yjnqkhj
            yjnqkhj: {
              yysrehj: 0.00,//营业（收入）额
              ynzzsehj: 0.00,//应缴纳增值税
              yjnzzsehj: 0.00,//已缴纳增值税
              jxsehj: 0.00,//进项税额
            },
          }
        }
      },
    };
  },
  computed: {

  },
  //子页面调用父页面方法
  watch: {
    // 监听数据变化
    'tableData.pageData.body.fzjgxx.yjnqk': {
      handler(newVal, oldVal) {
        var _self = this;
        _self.totalRows(
          'body.fzjgxx.yjnqk',
          'otherData.yjnqkhj'
        );
      },
      deep: true
    }
  },
  created: function () {
    this.initializationData = this.getSelectSbbInfo(); // 初始化 取值
    this.initializationDataAssignment(); // 初始化表内数据 赋值
    this.betweenTables(); // 表间数据处理
    this.inTableVerification(); // 表内校验
    this.initializationOfPublicServices()  //  暂时替换header节点
  },
  mounted: function () {
    this.keyboardSwitchingCell('taxTable', 'text'); // 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
    //this.getPdf(this.name,true);
  },
  methods: {
    // 初始化表内数据 赋值
    initializationDataAssignment: function () {
      this.tableData.pageData.head.publicHead.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh;//纳税人识别号
      this.tableData.pageData.head.publicHead.nsrmc = this.initializationData.init.jbxx[0].nsrmc;//纳税人名称
      this.tableData.pageData.head.publicHead.sssq.rqQ = this.tableData.header.sksssjq;//税款所属期起
      this.tableData.pageData.head.publicHead.sssq.rqZ = this.tableData.header.sksssjz;//税款所属期止
      this.tableData.pageData.head.publicHead.tbrq = this.initializationData.openTableData.openTableDate  //填表日期
      this.tableData.pageData.checkData.sbqsrq = this.initializationData.data[0].pageData.checkData.sbqsrq;//申报起始日期
      this.tableData.pageData.checkData.sbjzrq = this.initializationData.data[0].pageData.checkData.sbjzrq;//申报截止日期
    },
    // 表间数据处理
    betweenTables: function () {

    },
    //初始化表内校验
    inTableVerification: function () {
      // 1：不能为空：分支机构纳税人识别号
      this.validationValue.push({ type: "fun", name: "nsrsbh", function: this.isNullNSRSBH });
      // 1：不能为空： 分机构纳税人名称
      this.validationValue.push({ type: "fun", name: "nsrmc", function: this.isNullNSRMC });
      // 1：不能为空：所属时期起
      this.validationValue.push({ type: "fun", name: "rqQ", function: this.isNullRQQ });
      // 1：不能为空：所属时期止
      this.validationValue.push({ type: "fun", name: "rqZ", function: this.isNullRQZ });
      // 2：“所属时期止”必须是当月的最后一天
      // 5："所属时期止"不能大于申报期截至日期
      this.validationValue.push({ type: "fun", name: "rqZ", function: this.endDate });
      // 3：“所属时期起”必须是当月的第一天
      // 4："所属时期起"不能小于申报期起始日期
      this.validationValue.push({ type: "fun", name: "rqQ", function: this.startDate });
    },
    // 1：不能为空：分支机构纳税人识别号
    isNullNSRSBH: function () {
      var hkzjgjscddGridlbVO = this.tableData.pageData.body.fzjgxx.yjnqk;
      for (var i = 0; i < hkzjgjscddGridlbVO.length; i++) {
        if (hkzjgjscddGridlbVO[i].fzjgNsrsbh == '' || hkzjgjscddGridlbVO[i].fzjgNsrsbh == null) {
          return `<div style="color:red" data-id="nsrsbh` + i + `" class="errorDiv">“分支机构纳税人识别号”不能为空！！</div><br>`;
        } else {
          if(i==hkzjgjscddGridlbVO.length-1){
            return true;
          }
        }
      }
    },
    // 1：不能为空： 分机构纳税人名称
    isNullNSRMC: function () {
      var hkzjgjscddGridlbVO = this.tableData.pageData.body.fzjgxx.yjnqk;
      for (var i = 0; i < hkzjgjscddGridlbVO.length; i++) {
        if (hkzjgjscddGridlbVO[i].nsrmc == '' || hkzjgjscddGridlbVO[i].nsrmc == null) {
          return `<div style="color:red" data-id="nsrmc` + i + `" class="errorDiv">“分机构纳税人名称”不能为空！！</div><br>`;
        } else {
          if(i==hkzjgjscddGridlbVO.length-1){
            return true;
          }
        }
      }
    },
    // 1：不能为空：所属时期起
    isNullRQQ: function () {
      var hkzjgjscddGridlbVO = this.tableData.pageData.body.fzjgxx.yjnqk;
      for (var i = 0; i < hkzjgjscddGridlbVO.length; i++) {
        if (hkzjgjscddGridlbVO[i].sssq.rqQ == '' || hkzjgjscddGridlbVO[i].sssq.rqQ == null) {
          return `<div style="color:red" data-id="rqQ` + i + `" class="errorDiv">“所属时期起”不能为空！！</div><br>`;
        } else {
          if(i==hkzjgjscddGridlbVO.length-1){
            return true;
          }
        }
      }
    },
    // 1：不能为空：所属时期止
    isNullRQZ: function () {
      var hkzjgjscddGridlbVO = this.tableData.pageData.body.fzjgxx.yjnqk;
      for (var i = 0; i < hkzjgjscddGridlbVO.length; i++) {
        if (hkzjgjscddGridlbVO[i].sssq.rqZ == '' || hkzjgjscddGridlbVO[i].sssq.rqZ == null) {
          return `<div style="color:red" data-id="rqZ` + i + `" class="errorDiv">“所属时期止”不能为空！！</div><br>`;
        } else {
          if(i==hkzjgjscddGridlbVO.length-1){
            return true;
          }
        }
      }
    },
    // 2：“所属时期止”必须是当月的最后一天
    // 5："所属时期止"不能大于申报期截至日期
    endDate: function () {
      var hkzjgjscddGridlbVO = this.tableData.pageData.body.fzjgxx.yjnqk;
      var lastDay = this.getLastDay();
      var sbjzrq = this.tableData.pageData.checkData.sbjzrq;//申报截止日期
      for (var i = 0; i < hkzjgjscddGridlbVO.length; i++) {
        var rqZ = hkzjgjscddGridlbVO[i].sssq.rqZ;
        // console.log('tab(rqZ,fristDay)',this.tab(rqZ,lastDay));
        if (rqZ != lastDay) {
          return `<div style="color:red" data-id="rqZ` + i + `" class="errorDiv">“所属时期止”必须是当月的最后一天！！</div><br>`;
        } else if (rqZ > sbjzrq) {
          return `<div style="color:red" data-id="rqZ` + i + `" class="errorDiv">"所属时期止"不能大于申报期截至日期！！</div><br>`;
        } else {
          if(i==hkzjgjscddGridlbVO.length-1){
            return true;
          }
        }
      }
    },
    // 3：“所属时期起”必须是当月的第一天
    // 4："所属时期起"不能小于申报期起始日期
    startDate: function () {
      var hkzjgjscddGridlbVO = this.tableData.pageData.body.fzjgxx.yjnqk;
      var sbqsrq = this.tableData.pageData.checkData.sbqsrq;//申报起始日期
      var fristDay = this.getFirstDay();
      for (var i = 0; i < hkzjgjscddGridlbVO.length; i++) {
        var rqQ = hkzjgjscddGridlbVO[i].sssq.rqQ;
        if (rqQ != fristDay) {
          return `<div style="color:red" data-id="rqQ` + i + `" class="errorDiv">“所属时期起”必须是当月的第一天！！</div><br>`;
        } else if (rqQ < sbqsrq) {
          return `<div style="color:red" data-id="rqQ` + i + `" class="errorDiv">"所属时期起"不能小于申报期起始日期！！</div><br>`;
        } else {
          if(i==hkzjgjscddGridlbVO.length-1){
            return true;
          }
        }
      }
    },
    getFirstDay: function () {
       var date = new Date();
        date.setDate(1);
        var month = parseInt(date.getMonth()+1);
        var day = date.getDate();
        if (month < 10) {
            month = '0' + month
        }
        if (day < 10) {
            day = '0' + day
        }
        return date.getFullYear() + '-' + month + '-' + day;
    },
    getLastDay: function () {
      var date=new Date();
        var currentMonth=date.getMonth();
        var nextMonth=++currentMonth;
        var nextMonthFirstDay=new Date(date.getFullYear(),nextMonth,1);
        var oneDay=1000*60*60*24;
        var lastTime = new Date(nextMonthFirstDay-oneDay);
        var month = parseInt(lastTime.getMonth()+1);
        var day = lastTime.getDate();
        if (month < 10) {
            month = '0' + month
        }
        if (day < 10) {
            day = '0' + day
        }
        return date.getFullYear() + '-' + month + '-' + day;
    },
    // 日期比较
    tab(date1,date2){
      var oDate1 = new Date(date1);
      var oDate2 = new Date(date2);
      if(oDate1.getTime() > oDate2.getTime()){
          console.log('第一个大');
      } else {
          console.log('第二个大');
      }
    }
  },
  provide() {
    return {
      resetData: this.resetData
    };
  }
};
</script>

<style scoped>
</style>




