      //应税货物及劳务的本年累计＝同行次中上期的应税货物及劳务的本年累计+本期数，其中第18、19栏次除外
      //第22栏3列  ynsehj
      Ynsehj2:function () {
        if( this.tableData.pageData.checkData.wq="undefined"){
          this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[2].ynsehj =  parseFloat(this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[0].ynsehj).toFixed(2)

        }else{
          this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[2].ynsehj = (parseFloat(this.tableData.pageData.checkData.wq.pageData.zzsxgmGrid.zzsxgmGridlb[2].ynsehj) + parseFloat(this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[0].ynsehj)).toFixed(2)

        }
        return  this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[2].ynsehj
      },
      //第22栏4列  ynsehj
      Ynsehj3:function () {
        if( this.tableData.pageData.checkData.wq="undefined"){
          this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[3].ynsehj = parseFloat(this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[1].ynsehj).toFixed(2)

        }else{
          this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[3].ynsehj = (parseFloat(this.tableData.pageData.checkData.wq.pageData.zzsxgmGrid.zzsxgmGridlb[3].ynsehj) + parseFloat(this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[1].ynsehj)).toFixed(2)

        }
        return  this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[3].ynsehj
      },
    },
    watch:{
      "tableData.pageData.checkData.bdcxse": {
        handler(newVal, oldVal) {
          this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[1].bdcxse = newVal;   //newVal == 0.00  == oldValue
          if(newVal!=oldVal && newVal!= 0){
            this.tableData.pageData.checkData.J17 = "1"
            console.log( this.tableData.pageData.checkData.J17)

          }else{
            this.tableData.pageData.checkData.J17 = "0"
          }
        },
      },



    },

    mounted: function () {
      this.keyboardSwitchingCell("taxTable","text");// 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
      //this.getPdf(this.name,true);
    },
    created:function () {
     this.initializationData = this.getSelectSbbInfo();
      this.initializationOfPublicServices();//  暂时替换header节点
      this.$elementUi.MessageBox.alert("尊敬的纳税人，您好！为全面贯彻党中央国务院减税降费决策部署，落实好提高增值税小规模纳税人起征点政策，申报系统会帮助您判断是否可以享受免征增值税政策，在您申报的过程中可能会出现以下提示信息，请您认真阅读，并按照相关指引进行操作，非常感谢您的支持配合！","提示",{type: 'info'});
      this.tableData.pageData.otherData.bdcxse =  this.tableData.pageData.zzsxgmGrid.zzsxgmGridlb[1].bdcxse;  //本期销售不动产
      this.initializationDataAssignment();  //初始化数据
      //this.betweenTables();  //表间数据
      this.inTableVerification();  // 表内校验
    }
  }
</script>

