<template>
  <el-container>
    <el-header>
      <app-header :parentData="tableData" :parentValidation="validationValue" :parentPdfName="name"></app-header>
    </el-header>
    <el-container>
      <el-main id="allTable">
        <el-scrollbar style="height: 100%;">
          <div style="padding: 20px ;background-color: #fff" id="pdfDom" name="pdfDomBranch" class="pdfDomBranch">
            <table cellspacing="0" cellpadding="0" width="1210" height="354" id="taxTable">
              <tr>
                <td width="925" height="46" align="center" valign="middle" colspan="6" class="noBorder">
                  <B>航空运输企业分支机构传递单</B>
                </td>
              </tr>
              <tr>
                <td width="420" height="22" align="left" valign="middle" class="noBorder">
                  分支机构名称：（签章）
                  <span class="name"></span>
                </td>
                <td width="391" height="22" align="left" valign="middle" colspan="3" class="noBorder"></td>
                <td width="131" height="22" align="left" valign="middle" colspan="2" class="noBorder">纳税人识别号：<span v-html="tableData.pageData.sbbhead.nsrsbh"></span></td>
              </tr>
              <tr>
                <td width="233" height="22" align="center" valign="middle" class="noBorder">&nbsp;</td>
                <td width="391" height="22" align="center" valign="middle" colspan="3" class="noBorder">
                  税款所属时间：
                  <span v-format-date v-html="tableData.pageData.sbbhead.skssqq"></span>
                  至
                  <span v-format-date v-html="tableData.pageData.sbbhead.skssqz"></span>
                </td>
                <td width="131" height="22" align="center" valign="middle" class="noBorder">&nbsp;</td>
                <td width="190" height="22" align="right" valign="middle" class="noBorder">金额单位：元(至角、分)</td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="925" height="22" align="center" valign="middle" colspan="6">已缴纳增值税情况</td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="233" height="22" align="center" valign="middle">征税项目</td>
                <td class="uneditable-bgColor" width="130" height="22" align="center" valign="middle">销售额</td>
                <td class="uneditable-bgColor" width="131" height="22" align="center" valign="middle">预征率</td>
                <td class="uneditable-bgColor" width="130" height="22" align="center" valign="middle">预缴税额</td>
                <td class="uneditable-bgColor" width="131" height="22" align="center" valign="middle">补缴税额</td>
                <td class="uneditable-bgColor" width="170" height="22" align="center" valign="middle">备注</td>
              </tr>
              <tr v-for="(item,index) in tableData.pageData.yjnzzsqkGrid.yjnzzsqkGridlbVO" :key="index+'jsxm'">
                <td class="uneditable-bgColor" width="233" height="22" align="left" valign="middle" v-html="item.zsxmWord"></td>
                <td class="editable-bgColor" width="130" height="22" align="right" valign="middle">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" :data-id="'xse'+index" @blur="retainValidDecimal('yjnzzsqkGrid.yjnzzsqkGridlbVO['+index+'].xse','2')"  v-model="item.xse" />
                </td>
                <td class="editable-bgColor" width="131" height="22" align="right" valign="middle">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" v-percent="2" :data-id="'yzl'+index" @blur="retainValidDecimal('yjnzzsqkGrid.yjnzzsqkGridlbVO['+index+'].yzl','2')" v-model="item.yzl" />
                </td>
                <td class="font-color-red" width="130" height="22" align="right" valign="middle" v-floatOld="2" :data-id="'yjse1'+index" v-html="item.yjse1" :text="yjseVal(index)"></td>
                <td class="uneditable-bgColor" width="131" height="22" align="center" valign="middle">--</td>
                <td class="editable-bgColor" width="170" height="22" align="center" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'bz'+index" v-model="item.bz" />
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="233" height="22" align="center" valign="middle">小计</td>
                <td class="font-color-red" width="130" height="22" align="right" valign="middle" v-floatOld="2" data-id="xsehj" v-html="tableData.pageData.checkData.yjnzzsqkGridlbVOhj.xsehj"></td>
                <td class="uneditable-bgColor" width="131" height="22" align="center" valign="middle">--</td>
                <td class="font-color-red" width="130" height="22" align="right" valign="middle" v-floatOld="2" data-id="yjse1hj" v-html="tableData.pageData.checkData.yjnzzsqkGridlbVOhj.yjse1hj"></td>
                <td class="uneditable-bgColor" width="131" height="22" align="center" valign="middle">--</td>
                <td class="uneditable-bgColor" width="170" height="22" align="center" valign="middle">--</td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="925" height="22" align="center" valign="middle" colspan="6">取得进项税额情况</td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="233" height="22" align="center" valign="middle">发票代码及号码/缴款书号码</td>
                <td class="uneditable-bgColor" width="130" height="22" align="center" valign="middle">金额</td>
                <td class="uneditable-bgColor" width="131" height="22" align="center" valign="middle">进项税额</td>
                <td class="uneditable-bgColor" width="130" height="22" align="center" valign="middle">认证/稽核月份</td>
                <td class="uneditable-bgColor" width="301" height="22" align="center" valign="middle" colspan="2">备注</td>
              </tr>
              <tr v-for="(item,index) in tableData.pageData.qdjxseqkGrid.dkqdGridlbVO" :key="index+'jxse'">
                <td class="editable-bgColor" width="233" height="22" align="center" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'fpdmjhmhjkshm'+index" v-model="item.fpdmjhmhjkshm" /></td>
                <td class="editable-bgColor" width="130" height="22" align="right" valign="middle">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" :data-id="'je'+index" @blur="retainValidDecimal('qdjxseqkGrid.dkqdGridlbVO['+index+'].je','2')" v-model="item.je" /></td>
                <td class="editable-bgColor" width="131" height="22" align="right" valign="middle">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" :data-id="'jxse'+index" @blur="retainValidDecimal('qdjxseqkGrid.dkqdGridlbVO['+index+'].jxse','2')" v-model="item.jxse" /></td>
                <td class="editable-bgColor" width="130" height="22" align="right" valign="middle">
                  <input type="text" class="input-border-bgColor" :data-id="'rzhjhyf'+index" v-model="item.rzhjhyf" /></td>
                <td class="editable-bgColor" width="301" height="22" align="center" valign="middle" colspan="2">
                  <input type="text" class="input-border-bgColor" :data-id="'bz'+index" v-model="item.bz" /></td>
                <td width="151" height="22" align="left" valign="middle" class="noBorder">
                  <addBtn :data-name="1" :data-num="index" :parentListData="tableData" :parentListKey="'qdjxseqkGrid.dkqdGridlbVO'" v-show="index == tableData.pageData.qdjxseqkGrid.dkqdGridlbVO.length - 1"></addBtn>
                  <deleteBtn :data-name="1" :data-num="index" :parentListData="tableData" :parentListKey="'qdjxseqkGrid.dkqdGridlbVO'" v-show="index != 0"></deleteBtn>
                </td>
              </tr>
              <tr>
                <td width="925" height="22" align="left" valign="middle" colspan="6" class="noBorder">注：本表一式四份，填报单位及其主管国税机关、总机构及其主管国税机关各一份。</td>
              </tr>
              <tr>
                <td width="925" height="22" align="right" valign="middle" colspan="6" class="noBorder">主管国税机关：（签章）</td>
              </tr>
            </table>
          </div>
        </el-scrollbar>
      </el-main>
      <el-aside class="bar">
        <informationBar :parentName="name"></informationBar>
      </el-aside>
    </el-container>
  </el-container>
</template>

<script>
/**
 * @Author lmz
 * @Description 航空运输企业试点地区分支机构传递单
 * @Date
 **/
import Header from "../../app-header";
import informationBar from "../../informationBar.vue";
import router from "../../../router/index.js";
import addBtn from "../../addBtn.vue";
import deleteBtn from "../../deleteBtn.vue";

var linq = require("linq");
export default {
  name: "10101_018_10",
  components: {
    "app-header": Header,
    informationBar: informationBar,
    addBtn: addBtn,
    deleteBtn: deleteBtn
  },
  props: ["routerParam"],
  data: function () {
    return {
      certificateDictionary: "", //身份字典
      cityDictionary: "",
      validationValue: [], //校验规则
      requiredItems: [], //必填项目
      name: "10101_018_10", //文件名称
      mbyqndks: "", //弥补以前年度亏损
      initializationData: "", //初始化数据
      saveInfo: "", //已保存过的数据
      tableData: {
        header: {
          bbDm: "10101_018",
          version: "10",
          zsxmDm: "10",
          bblxDm: "22",
          nsrmc: "",
          nsrsbh: "",
          tbrq: "",
          sksssjz: "",
          sksssjq: ""
        },
        pageData: {
          sbbhead: {
            nsrmc: "", //纳税人名称
            nsrsbh: "", //纳税人识别号
            skssqq: "", //税款所属期起
            skssqz: "" //税款所属期止
          },
          yjnzzsqkGrid: {
            // tableData.pageData.yjnzzsqkGrid.yjnzzsqkGridlbVO[]
            yjnzzsqkGridlbVO: [
              {
                zsxm: "0101", //征收项目 编码
                zsxmWord: "交通运输服务",//征收项目 名称
                xse: 0.0, //销售额
                yzl: 0.0, //预征率
                yjse1: 0.0, //预缴税额
                bz: "" //备注
              },
              {
                zsxm: "0102", //征收项目 编码
                zsxmWord: "有形动产租赁服务",//征收项目 名称
                xse: 0.0, //销售额
                yzl: 0.0, //预征率
                yjse1: 0.0, //预缴税额
                bz: "" //备注
              },
              {
                zsxm: "", //征收项目 编码
                zsxmWord: "其他《应税服务范围注释》所列业务",//征收项目 名称
                xse: 0.0, //销售额
                yzl: 0.0, //预征率
                yjse1: 0.0, //预缴税额
                bz: "" //备注
              },
            ]
          },
          qdjxseqkGrid: {
            // tableData.pageData.qdjxseqkGrid.dkqdGridlbVO[]
            dkqdGridlbVO: [
              {
                fzjgmc: "", //纳税人名称
                fpdmjhmhjkshm: "", //发票代码及号码/缴款书号码
                je: 0.0, //金额
                jxse: 0.0, //进项税额
                rzhjhyf: 0, //认证/稽核月份
                bz: "" //备注
              }
            ]
          },
          // 表间数据的校验
          checkData: {
            // 合计
            yjnzzsqkGridlbVOhj: {
              xsehj: 0.00,
              yjse1hj: 0.00
            },
            zbGridlbVO: {
              bqybtse: 1.00//主表第34行一般项目
            }
          },
          otherData: {
          }
        }
      }
    };
  },
  computed: {
    // 征收项目 {{item.zsxm === '0101' ? '交通运输服务' : (item.zsxm === '0102' ? '有形动产租赁服务' : '其他《应税服务范围注释》所列业务')}}
    zsxmDm() {
      return function (zsxmDm) {
        if (zsxmDm === '0101') {
          this.tableData.pageData.otherData.zsxmWord.zsxmDm = '交通运输服务';
        } else if (zsxmDm === '0102') {
          this.tableData.pageData.otherData.zsxmWord.zsxmDm = '有形动产租赁服务';
        } else {
          this.tableData.pageData.otherData.zsxmWord.zsxmDm = '其他《应税服务范围注释》所列业务';
        }
        return this.tableData.pageData.otherData.zsxmWord.zsxmDm;
      }
    },
    // 表内公式 3：预缴税额=销售额×预征率
    yjseVal() {
      return function (index) {
        var xse = this.tableData.pageData.yjnzzsqkGrid.yjnzzsqkGridlbVO[index].xse;
        var yzl = this.tableData.pageData.yjnzzsqkGrid.yjnzzsqkGridlbVO[index].yzl;
        var yjse1 = parseFloat(xse) * parseFloat(yzl);
        this.tableData.pageData.yjnzzsqkGrid.yjnzzsqkGridlbVO[index].yjse1 = yjse1.toFixed(2);
        return this.tableData.pageData.yjnzzsqkGrid.yjnzzsqkGridlbVO[index].yjse1;
      }
    }
  },
  //子页面调用父页面方法
  watch: {
    // 监听数据变化
    'tableData.pageData.yjnzzsqkGrid.yjnzzsqkGridlbVO': {
      handler(newVal, oldVal) {
        var _self = this;
        // 表内公式 1：合计=销售额之和，2：合计=预缴税额之和
        _self.totalRows(
          'yjnzzsqkGrid.yjnzzsqkGridlbVO',
          'checkData.yjnzzsqkGridlbVOhj'
        );
      },
      deep: true
    }
  },
  created: function () {
    this.initializationData = this.getSelectSbbInfo(); // 初始化 取值
    this.initializationDataAssignment(); // 初始化表内数据 赋值
    this.betweenTables(); // 表间数据处理
    this.inTableVerification(); // 表内校验
    this.initializationOfPublicServices()  //  暂时替换header节点
  },
  mounted: function () {
    this.keyboardSwitchingCell("taxTable", "text"); // 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
    //this.getPdf(this.name,true);
  },
  methods: {
    // 初始化表内数据 赋值
    initializationDataAssignment: function () {
      this.tableData.pageData.sbbhead.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh    //纳税人识别号
      this.tableData.pageData.sbbhead.nsrmc = this.initializationData.init.jbxx[0].nsrmc     //纳税人名称
      this.tableData.pageData.sbbhead.skssqq = this.tableData.header.sksssjq//税款所属期起
      this.tableData.pageData.sbbhead.skssqz = this.tableData.header.sksssjz//税款所属期止
    },
    // 表间数据处理
    betweenTables: function () {

    },
    //初始化表内校验
    inTableVerification: function () {
      // 表内校验 1：认证/稽核月份必须在1—12之间。
      this.validationValue.push({ type: "fun", name: "rzhjhyf", function: this.monthNum });
      // 表内校验 2：“发票代码及号码/缴款书号码”不能为空！！
      this.validationValue.push({ type: "fun", name: "fpdmjhmhjkshm", function: this.isNullHM });
      // 表内校验 3：1＞预征率≥0
      this.validationValue.push({ type: "fun", name: "yzl", function: this.yzlVal });
      // 表间公式 预缴税额小计≤主表第34行一般项目
      this.validationValue.push({ type: "fun", name: "yjse1hj", function: this.yjsehjVal });
    },
    // 表内校验 1：认证/稽核月份必须在1—12之间。
    monthNum: function () {
      var dkqdGridlbVO = this.tableData.pageData.qdjxseqkGrid.dkqdGridlbVO;
      var reg = /^(1[0-2]|[1-9])$/;
      for (var i = 0; i < dkqdGridlbVO.length; i++) {
        if (reg.test(dkqdGridlbVO[0].rzhjhyf)) {
          if(i==dkqdGridlbVO.length-1){
            return true;
          }
        } else {
          return `<div style="color:red" data-id="rzhjhyf` + i + `" class="errorDiv">认证/稽核月份必须在1—12之间。</div><br>`;
        }
      }
    },
    // 表内校验 2：“发票代码及号码/缴款书号码”不能为空！！
    isNullHM: function () {
      var dkqdGridlbVO = this.tableData.pageData.qdjxseqkGrid.dkqdGridlbVO;
      for (var i = 0; i < dkqdGridlbVO.length; i++) {
        if (dkqdGridlbVO[i].fpdmjhmhjkshm == '' || dkqdGridlbVO[i].fpdmjhmhjkshm == null) {
          return `<div style="color:red" data-id="fpdmjhmhjkshm` + i + `" class="errorDiv">“发票代码及号码/缴款书号码”不能为空！！</div><br>`;
        } else {
          if(i==dkqdGridlbVO.length-1){
            return true;
          }
        }
      }
    },
    // 表内校验 3：1＞预征率≥0
    yzlVal: function () {
      var dkqdGridlbVO = this.tableData.pageData.qdjxseqkGrid.dkqdGridlbVO;
      var reg = /0.[0-9]{1,2}|0/;
      for (var i = 0; i < dkqdGridlbVO.length; i++) {
        if (reg.test(dkqdGridlbVO[i].yzl)) {
          if(i==dkqdGridlbVO.length-1){
            return true;
          }
        } else {
          return `<div style="color:red" data-id="yzl` + i + `"  class="errorDiv">1＞预征率≥0</div><br>`;
        }
      }
    },
    // 表间公式 预缴税额小计≤主表第34行一般项目
    yjsehjVal: function () {
      var yjse1hj = this.tableData.pageData.checkData.yjnzzsqkGridlbVOhj.yjse1hj;
      var bqybtse = this.tableData.pageData.checkData.zbGridlbVO.bqybtse;
      if (yjse1hj <= bqybtse) {
        return true;
      } else {
        return `<div style="color:red" data-id="yjse1hj" class="errorDiv">1&lt;预缴税额小计≤主表第34行一般项目</div><br>`;
      }
    }
  },
  provide() {
    return {
      resetData: this.resetData
    };
  }
};
</script>

<style scoped>
</style>




