      var flzlData1 = this.tableData.pageData.checkData.flzlData1.zbCol21Row1;
      var zbGridlbVO = this.tableData.pageData.zbGrid.zbGridlbVO;
      if (zbGridlbVO[0].jybfYnse != flzlData1) {
        return `<div style="color:red" class="errorDiv">第21行第1列＝《附表一》（第10列第8、9a、10、11行之和-第10列第14行）+（第14列第9b、12、13a、13b行之和-第14列第15行）</div><br>`;
      } else {
        return true;
      }
    },
    /* ????? 表间公式 18.第21行第3列＝《附列资料（一）》第10列第14行+第14列第15行，校验写在第21行第3列*/
    row21Col3: function () {
      var flzlData1 = this.tableData.pageData.checkData.flzlData1.zbCol21Row3;
      var zbGridlbVO = this.tableData.pageData.zbGrid.zbGridlbVO;
      if (zbGridlbVO[2].jybfYnse != flzlData1) {
        return `<div style="color:red" class="errorDiv">第21行第3列＝《附列资料（一）》第10列第14行+第14列第15行</div><br>`;
      } else {
        return true;
      }
    },
    /* ????? 表间公式 19.第23行第1列＝《减免税申报明细表》“一、减税项目”第4列“本期实际抵减税额_合计”，
    校验写在第23行第1列*/
    row23Col1: function () {
      var hjCol4 = this.tableData.pageData.checkData.jmssbmxb.hjCol4;
      var zbGridlbVO = this.tableData.pageData.zbGrid.zbGridlbVO;
      if (zbGridlbVO[0].ynsejze != hjCol4) {
        return `<div style="color:red" class="errorDiv">第23行第1列＝《减免税申报明细表》“一、减税项目”第4列“本期实际抵减税额_合计”</div><br>`;
      } else {
        return true;
      }
    }
  },
  provide() {
    return {
      resetData: this.resetData
    };
  }
};
</script>

