           this.tableData.pageData.zcsmForm.bl = parseFloat(this.tableData.pageData.zcsmForm.fwxsehj)/parseFloat(this.tableData.pageData.zcsmForm.xsehj)
          }else if(parseFloat(this.tableData.pageData.zcsmForm.fwxsehj)>0&&parseFloat(this.tableData.pageData.zcsmForm.xsehj)>0){
            this.tableData.pageData.zcsmForm.bl = parseFloat(this.tableData.pageData.zcsmForm.fwxsehj)/parseFloat(this.tableData.pageData.zcsmForm.xsehj)
          }
          return this.tableData.pageData.zcsmForm.bl
        }
      },

      //初始化表内校验
      inTableVerification:function(){
        //4）计算期起不应早于2018年4月   xsejsqq   xsejsqz

        //5）计算期起不应早于纳税人设立年月

        //6）校验计算期起不应晚于适用政策有效期起

        //7）校验计算期止不应早于计算期起，则系统提示：计算期止不应早于计算期起

        //8）校验计算期起至计算期止不应少于3个月

        //占比≤50%时，系统强制提示不符合政策规定
        this.validationValue.push({type:"fun",name:"zcsmForm.fwxsehj",function:this.zbFun,reportErrors:"占比≤50%，不符合政策规定",color:"red",})
        //四项服务销售额≤0时，系统强制提示不符合政策规定
        this.validationValue.push({type:"fun",name:"zcsmForm.fwxsehj",function:this.sxfwFun,reportErrors:"四项服务销售额≤0，不符合政策规定",color:"red",})
      },
      zbFun(){
          var flag= true;
          if( parseFloat(this.tableData.pageData.zcsmForm.bl)<= 0.5){

          }else{
            flag= false
          }
          return flag
      },
      sxfwFun(){
        var flag= true;
        if( parseFloat(this.tableData.pageData.zcsmForm.fwxsehj)<= 0){

        }else{
          flag= false
        }
        return flag
      },


      created: function () {
         this.initializationData = this.getSelectSbbInfo();
        this.initializationDataAssignment(); //初始化数据
        this.inTableVerification(); // 表内校验
        this.betweenTables(); //表间数据
        this.initializationOfPublicServices(); // 初始化公共业务模块

      },
      mounted: function () {
        this.keyboardSwitchingCell("taxTable", "text"); // 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
        //this.getPdf(this.name,true);
        compareTime();

      },
      methods: {
        //初始化数据赋值
        initializationDataAssignment:function () {
          this.tableData.pageData.sbbhead.nsrmc =this.initializationData.init.jbxx[0].nsrmc     //纳税人名称
          alert( this.tableData.pageData.sbbhead.nsrmc)
          this.tableData.pageData.sbbhead.nsrsbh =this.initializationData.init.jbxx[0].nsrsbh     //纳税人识别号
          // // this.tableData.pageData.otherData.tbrq = this.initializationData.openTableData.openTableDate  //填表日期
          this.tableData.pageData.sbbhead.skssqq =  this.tableData.header.sksssjq
          this.tableData.pageData.sbbhead.skssqz =  this.tableData.header.sksssjz

        },
        //比较日期的起止方法   没有用
        compareTime: function () {
          var starTime = this.tableData.pageData.zcsmForm.yxqq;
          var endTime = this.tableData.pageData.zcsmForm.yxqz;
          if ((starTime != "" || starTime != null) && (endTime != "" || endTime != null)) {
            var startDay = this.dataToTimestamp(starTime);
            var endDay = this.dataToTimestamp(endTime);
            if (startDay > endDay) {
              this.$elementUi.MessageBox.alert(
                "“计算期止不应早于计算期起”！",
                "提示",
                { type: "info" }
              );
            }
          }
        },

      },
      provide() {
        return {
          resetData: this.resetData
        };
      }
    };
  </script>

