          var xse4 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[4].xse)
          this.tableData.pageData.otherData.trhj.xsehj= (parseFloat(xse0)+parseFloat(xse1)+parseFloat(xse2)+parseFloat(xse3)+parseFloat(xse4)).toFixed(2)
          return this.tableData.pageData.otherData.trhj.xsehj
      },
      ////扣除金额合计
      kcjehjFun(){
        var kcje0 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[0].kcje) ;
        var kcje1 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[1].kcje) ;
        var kcje2 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[2].kcje) ;
        var kcje3 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[3].kcje) ;
        var kcje4 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[4].kcje) ;
        this.tableData.pageData.otherData.trhj.kcjehj=(parseFloat(kcje0)+parseFloat(kcje1)+parseFloat(kcje2)+parseFloat(kcje3)+parseFloat(kcje4)).toFixed(2)
        return this.tableData.pageData.otherData.trhj.kcjehj
      },
      //预征税额合计
      yzsehjFun(){
        var yzse0 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[0].yzse) ;
        var yzse1 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[1].yzse) ;
        var yzse2 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[2].yzse) ;
        var yzse3 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[3].yzse) ;
        var yzse4 =parseFloat(this.tableData.pageData.yzxmGridVO.yzxmItemVO[4].yzse) ;
        this.tableData.pageData.otherData.trhj.yzsehj= (parseFloat(yzse0)+parseFloat(yzse1)+parseFloat(yzse2)+parseFloat(yzse3)+parseFloat(yzse4)).toFixed(2)
        return this.tableData.pageData.otherData.trhj.yzsehj
      },
      //5.第2行第4列单元格条件只读：税款所属时间为2018年04月01日至2018年06月30日，第2行第4列放开填写，否则锁死。 在所在单元格直接处理

    },
    methods: {
      //初始化表内校验
      inTableVerification:function(){
        this.validationValue.push({type:"null",name:"sbbhead.nsrmc",reportErrors:"“纳税人名称：（公章）”不能为空 ",color:"red",comparisons:0})

      },
      blrysfzjlxChange(val){
        alert("!!!!")
        this.tableData.pageData.otherData.blrysfzjlx2=val.dmmc;
      },

      //初始化数据赋值
      initializationDataAssignment:function () {
        this.tableData.pageData.sbbhead.nsrmc =this.initializationData.init.jbxx[0].nsrmc     //纳税人名称
        this.tableData.pageData.sbbhead.nsrsbh =this.initializationData.init.jbxx[0].nsrsbh     //纳税人识别号
        this.tableData.pageData.otherData.tbrq = this.initializationData.openTableData.openTableDate  //填表日期
        //下拉框数据 ----目前是空
        this.tableData.pageData.checkData.sfzsj = this.initializationData.init.dmlb.dmb_sfzjlx
        console.log(this.tableData.pageData.checkData.sfzsj )
        this.tableData.pageData.sbbhead.skssqq =  this.tableData.header.sksssjq
        this.tableData.pageData.sbbhead.skssqz =  this.tableData.header.sksssjz

      },


    },
    provide() {
      return {
        resetData: this.resetData
      };
    },
    mounted: function () {
      this.keyboardSwitchingCell("taxTable", "text"); // 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
      //this.getPdf(this.name,true);


    },
    created: function () {
      this.initializationData = this.getSelectSbbInfo();
      this.initializationOfPublicServices(); // 初始化公共业务模块
      this.initializationDataAssignment();  //初始化数据赋值
      this.inTableVerification();//整理校验规则

    },
  };
</script>

