    },
    //子页面调用父页面方法
    provide(){
      return{
        resetData:this.resetData
      }
    },
    methods:{
      //重置页面数据--初始化数据和隐藏列数据不重置
      resetData:function(){
        alert("1");
      },


      //初始化表内校验
      inTableVerification:function(){
        //（1）2.“减免额”应≤“本期应纳税额”
        this.validationValue.push({type:"fun",name:"flzlForm.jmse",function:this.jmsXybqynse,reportErrors:"减免额应≤本期应纳税额",color:"red",comparisons:0})
        //3.第9列应≥0
        this.validationValue.push({type:">=",name:"yhssb.yhssbGrid.yhssbGridlb.phjmse",reportErrors:"第9列应≥0	",color:"red",comparisons:0})

      },
      //2.“减免额”应≤“本期应纳税额”
      jmsXybqynse:function(){
        var yhssbGridlb = this.tableData.pageData.yhssb.yhssbGrid.yhssbGridlb;
        for(var i=0;i<yhssbGridlb.length;i++){
          var flag = true;
          if(yhssbGridlb[i].bqynse < yhssbGridlb[i].jmse){
            flag = false;
          }
          return flag;
        }

      }

    },
    computed:{
      //第1、2、3、4、5、8、9a、9b、10、11、12行第2列=第1行第1列×对应的税率
      //第1、2、3、4、5、8、9a、9b、10、11、12行第4列=第1行第3列×对应的税率
      //第1、2、3、4、5、8、9a、9b、10、11、12行第6列=第1行第5列×对应的税率
      //4.第1、2、3、4、5行第8列=第1行第7列×对应的税率，自动计算之后可以修改
      col12Row2468:function(){   //1 2行  2  4  6列  col12Row246()
        return function(colNum,keyName2,keyName1){
          var bqxsqkmxbGridlbVO= this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
           bqxsqkmxbGridlbVO[keyName1] = parseFloat(bqxsqkmxbGridlbVO[keyName2]) * 0.13;
          return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum][keyName1];
        }
      },
      col34Row2468:function(){   //3 4行  col34Row246()
        return function(colNum,keyName2,keyName1) {
           var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
           bqxsqkmxbGridlbVO[keyName1] = parseFloat(bqxsqkmxbGridlbVO[keyName2]) * 0.09;
           return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum][keyName1];
        }
      },
      col58Row2468:function(){   //5 8行   col58Row246()
        return function(colNum,keyName2,keyName1) {
           var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
           bqxsqkmxbGridlbVO[keyName1] = parseFloat(bqxsqkmxbGridlbVO[keyName2]) * 0.06;
           return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum][keyName1];
        }
      },
      col99Row246:function(){   //9a、9b行  col99Row246()
        return function(colNum,keyName2,keyName1) {
           var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
           bqxsqkmxbGridlbVO[keyName1] = parseFloat(bqxsqkmxbGridlbVO[keyName2]) * 0.05;
           return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum][keyName1];
        }
      },
      col10Row246:function(){   //10行   col10Row246()
        return function(colNum,keyName2,keyName1) {
           var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
           bqxsqkmxbGridlbVO[keyName1] = parseFloat(bqxsqkmxbGridlbVO[keyName2]) * 0.04;
           return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum][keyName1];
        }
      },
      col112Row246:function(){   //11  12行   col112Row246()
        return function(colNum,keyName2,keyName1) {
           var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
           bqxsqkmxbGridlbVO[keyName1] = parseFloat(bqxsqkmxbGridlbVO[keyName2]) * 0.03;
           return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum][keyName1];
        }
      },
      //5.第9列=第1列+第3列+第5列+第7列，除第6、7、14、15行是自行填写
      colAllRow9:function () {
        return function(colNum){
          var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
            bqxsqkmxbGridlbVO.xse = parseFloat(bqxsqkmxbGridlbVO.kjskzzszyfpXse)+
            parseFloat(bqxsqkmxbGridlbVO.kjqtfpXse)+parseFloat(bqxsqkmxbGridlbVO.wkjfpXse)
            +parseFloat(bqxsqkmxbGridlbVO.nsjctzdxse);
          return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum].xse;
        }
      },
      //6.第10列=第2列+第4列+第6列+第8列，除第6、7、14、15行是自行填写
      colAllRow10:function () {
        return function(colNum){
          var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
          bqxsqkmxbGridlbVO.hjXxynse = parseFloat(bqxsqkmxbGridlbVO.kjskzzszyfpXxynse)+
            parseFloat(bqxsqkmxbGridlbVO.kjqtfpXxynse)+parseFloat(bqxsqkmxbGridlbVO.wkjfpXxynse)
            +parseFloat(bqxsqkmxbGridlbVO.nsjctzXxynse);
          return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum].hjXxynse;
        }
      },
      //7.第11列=第9列+第10列
      colAllRow11:function () {
        return function(colNum){
          var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
          bqxsqkmxbGridlbVO.jshj = parseFloat(bqxsqkmxbGridlbVO.hjXxynse)+parseFloat(bqxsqkmxbGridlbVO.xse);
          return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum].jshj;
        }
      },
      //第13列=第11列-第12列
      colAllRow13:function () {
        return function(colNum){
          var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
          bqxsqkmxbGridlbVO.kchHsmsxse = parseFloat(bqxsqkmxbGridlbVO.jshj)-parseFloat(bqxsqkmxbGridlbVO.ysfwkcxmbqsjkcje);
          return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum].kchHsmsxse;
        }
      },
      //第14列
      colAllRow14:function () {
        return function(colNum){
          var bqxsqkmxbGridlbVO = this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum];
          if(bqxsqkmxbGridlbVO.ysfwkcxmbqsjkcje==0){
            bqxsqkmxbGridlbVO.kchXxynse = bqxsqkmxbGridlbVO.hjXxynse
          }else{
            if(colNum==1){//2  第14列=第2行第13列÷（1+0.16）×0.16，
              bqxsqkmxbGridlbVO.kchXxynse=parseFloat(bqxsqkmxbGridlbVO.kchHsmsxse)/(1+0.16)*0.16
            }else if(colNum==3){//4  第14列=第4行第13列÷（1+0.1）×0.1
              bqxsqkmxbGridlbVO.kchXxynse=parseFloat(bqxsqkmxbGridlbVO.kchHsmsxse)/(1+0.1)*0.1
            }else if(colNum==4){//5  第14列=第5行第13列÷（1+0.06）×0.06
              bqxsqkmxbGridlbVO.kchXxynse=parseFloat(bqxsqkmxbGridlbVO.kchHsmsxse)/(1+0.16)*0.106
            }
            // else if(colNum==6){//7
            //   bqxsqkmxbGridlbVO.kchXxynse=parseFloat(bqxsqkmxbGridlbVO.kchHsmsxse)/(1+0.16)*0.106
            // }
            else if(colNum==9){//9b  第14列=第9b行第13列÷（1+0.05）×0.05
              bqxsqkmxbGridlbVO.kchXxynse=parseFloat(bqxsqkmxbGridlbVO.kchHsmsxse)/(1+0.05)*0.05
            }else if(colNum==12){ //12 第14列=第12行第13列÷（1+0.03）×0.03
              bqxsqkmxbGridlbVO.kchXxynse=parseFloat(bqxsqkmxbGridlbVO.kchHsmsxse)/(1+0.03)*0.03
            }else if(colNum==13||colNum== 14||colNum== 15){  //13a 13b 13c   第14列=第13行第9列×第13行预征率
              bqxsqkmxbGridlbVO.kchXxynse=parseFloat(bqxsqkmxbGridlbVO.hjXxynse)*0.1
            }
          }
          return this.tableData.pageData.bqxsqkmxbGrid.bqxsqkmxbGridlbVO[colNum].kchXxynse;
        }
      }
      /*********/
    },
    mounted: function () {
      this.keyboardSwitchingCell("taxTable","text");// 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
      //this.getPdf(this.name,true);
      this.tool.analyzingFixedColumns()  //固定行列
    },
    created:function () {
      this.inTableVerification();// 表内校验
    }
  }
</script>

<style scoped>
  #allTable{
    overflow: hidden;
  }
</style>




