      validationValue: [], //校验规则
      requiredItems: [], //必填项目
      name: '10103_001_10', //文件名称
      mbyqndks: '', //弥补以前年度亏损
      initializationData: '', //初始化数据
      saveInfo: '',//已保存过的数据
    };
  },
  computed: {

  },
  watch: {
    // 监听数据变化
  },
  created: function () {
    this.initializationData = this.getSelectSbbInfo(); // 初始化 取值
    this.initializationDataAssignment(); // 初始化表内数据 赋值
    this.inTableVerification(); // 表内校验
    this.betweenTables(); // 表间数据处理
    this.initializationOfPublicServices()  //  暂时替换header节点
  },
  mounted: function () {
    this.keyboardSwitchingCell('taxTable', 'text'); // 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
    //this.getPdf(this.name,true);
  },
  methods: {
    // 初始化表内数据
    initializationDataAssignment: function () {
      this.tableData.pageData.otherData.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh;//纳税人识别号
      this.tableData.pageData.otherData.nsrmc = this.initializationData.init.jbxx[0].nsrmc;//纳税人名称
      this.tableData.pageData.otherData.skssqq = this.initializationData.data[0].header.sksssjq;//税款所属期起
      this.tableData.pageData.otherData.skssqz = this.initializationData.data[0].header.sksssjz;//税款所属期止
      this.tableData.pageData.otherData.tbrq = this.initializationData.openTableData.openTableDate  //填表日期
    },
    // 表间数据处理
    betweenTables: function () {

    },
    //初始化表内校验
    inTableVerification: function () {

    },
  },
  provide() {
    //子页面调用父页面方法
    return {
      resetData: this.resetData
    };
  }
};
</script>

