        var zbListHj = parseFloat(zbList[0].bqybtse)+parseFloat(zbList[2].bqybtse);
        if(parseFloat(hjhj)!= parseFloat(zbListHj)){
          return `<div style="color:red" data-id="hjhj" class="errorDiv">本次应纳税额_增值税的合计应＝主表的第34行的“一般项目”和“即征即退项目”的合计。</div><br>`
        }else{
          return true;
        }
      },
      // 分支机构纳税人识别号
      isNullFzjgnsrsbh: function(){
        var listtqyzb = this.tableData.pageData.body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO;
        if(this.isNull(listtqyzb,'fzjgnsrsbh')==undefined){
          return true
        }else{
          return `<div style="color:red" class="errorDiv">分支机构纳税人识别号为必填项。</div><br>`
        }
      },
      //分支机构名称
      isNullFzjgmc: function(){
        var listtqyzb = this.tableData.pageData.body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO;
        if(this.isNull(listtqyzb,'fzjgmc')==undefined){
          return true
        }else{
          return `<div style="color:red" class="errorDiv">分支机构名称为必填项。</div><br>`
        }
      },
      //坐落区
      isNullZlqx: function(){
        var listtqyzb = this.tableData.pageData.body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO;
        if(this.isNull(listtqyzb,'zlqx')){
          // return true
        }else{
          return `<div style="color:red" class="errorDiv">坐落区为必填项。</div><br>`
        }
      },
      // 方法：不能为空
      isNull:function (arr,keyName){
        for (var i = 0;i<arr.length;i++){
          if(arr[i][keyName]==''|| arr[i][keyName]==null){
            return false;
          }
        }
      },

    },
    created:function () {
      this.initializationData = this.getSelectSbbInfo();
      this.inTableVerification();  // 表内校验
      this.$route.params.id;
      this.initializationDataAssignment();  //初始化数据
      this.betweenTables();  //表间数据
      this.initializationOfPublicServices()  //  暂时替换header节点

    }
  }
</script>
