        return `<div style="color:red" class="errorDiv">“当期允许抵扣农产品增值税进项税额购进农产品用于生产经营且不构成货物实体” 应≥0！</div><br>`;
      }else if(scjyJxse!=this.tableData.pageData.checkData.ncpscjyL8hj){
        return `<div style="color:red" class="errorDiv">“第1列_第3行”应=《购进农产品用于生产经营且不构成货物实体核定农产品增值税进项税额计算表》L8合计</div><br>`;
      }else{
        return true;
      }
    },
    // 5. 《农产品主表》合计 应=《增值税纳税申报表附列资料（二）》第6栏"税额"
    // 表间校验写在汇总表上了 flzlL6se dqyxdkjxsehj
    col1HJ: function(){
      var dqyxdkjxsehj = this.tableData.pageData.ncphdkczzsjxsejsbGrid.ncphdkczzsjxsejsbGridlbVO[0].dqyxdkjxsehj;
      if(dqyxdkjxsehj<0){
        return `<div style="color:red" class="errorDiv">“当期允许抵扣农产品增值税进项税额成本法” 应≥0！！</div><br>`;
      }else if(dqyxdkjxsehj!=this.tableData.pageData.checkData.flzlL6se){
        return `<div style="color:red" class="errorDiv">“第1列_第1行_成本法”应=《成本法核定农产品增值税进项税额计算表》L4合计</div><br>`;
      }else{
        return true;
      }
    },
  },
  provide() {
    return {
      resetData: this.resetData,
      getOtherData: this.getOtherData
    };
  }
};
</script>

