          this.validationValue.push({type:"fun",name:"body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO.fzjgmc",function:this.isNullFzjgmc}),
          this.validationValue.push({type:"fun",name:"body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO.zlqx",function:this.isNullZlqx}),

          this.validationValue.push({type:"null",name:"body.tynsyhbxxtqyzForm[0].tbdwz",reportErrors:"填报单位章为必填项",color:"red",comparisons:0}),
          this.validationValue.push({type:"null",name:"body.tynsyhbxxtqyzForm[0].tbrq",reportErrors:"填报日期为必填项",color:"red",comparisons:0}),
          this.validationValue.push({type:"null",name:"body.tynsyhbxxtqyzForm[0].jbr",reportErrors:"经办人为必填项",color:"red",comparisons:0}),
          this.validationValue.push({type:"null",name:"body.tynsyhbxxtqyzForm[0].jbrlxdh",reportErrors:"联系电话为必填项",color:"red",comparisons:0})
      },
      //合并列--分支机构情况
      rowSpaFun(){
        var trGridlbVOLen = this.tableData.pageData.hznsqyzzsfpbGrid.hznsqyzzsfpbGridlbVO.length;
        var rowSpa = parseFloat(trGridlbVOLen)+3;
        $(".fzjgqk").attr('rowspan',rowSpa);
      },



      // 分支机构纳税人识别号
      isNullFzjgnsrsbh: function(){
        var listtqyzb = this.tableData.pageData.body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO;
        if(this.isNull(listtqyzb,'fzjgnsrsbh')==undefined){
          return true
        }else{
          return `<div style="color:red" class="errorDiv">分支机构纳税人识别号为必填项。</div><br>`
        }
      },
      //分支机构名称
      isNullFzjgmc: function(){
        var listtqyzb = this.tableData.pageData.body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO;
        if(this.isNull(listtqyzb,'fzjgmc')==undefined){
          return true
        }else{
          return `<div style="color:red" class="errorDiv">分支机构名称为必填项。</div><br>`
        }
      },
      //坐落区
      isNullZlqx: function(){
        var listtqyzb = this.tableData.pageData.body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO;
        if(this.isNull(listtqyzb,'zlqx')){
          // return true
        }else{
          return `<div style="color:red" class="errorDiv">坐落区为必填项。</div><br>`
        }
      },
      // 方法：不能为空
      isNull:function (arr,keyName){
        for (var i = 0;i<arr.length;i++){
          if(arr[i][keyName]==''|| arr[i][keyName]==null){
            return false;
          }
        }
      },

    },
    created:function () {
      this.initializationData = this.getSelectSbbInfo();
      this.initializationOfPublicServices();  //  暂时替换header节点
      this.inTableVerification();
      this.$route.params.id;
      this.initializationDataAssignment();  //初始化数据
      this.betweenTables();  //表间数据

    }
  }
</script>
