        var qmkcZgsl = parseFloat(qckcZgsl) + parseFloat(bqrkZgsl) - parseFloat(bqckYsbfsl) - parseFloat(bqckFysbfZysl);
        this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].qmkcZgsl = qmkcZgsl.toFixed(2);
        return this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].qmkcZgsl;
      };
    },
    // 2：第16列=第2列+第6列－第12列
    col16() {
      return function (index) {
        var qckcDcsl = this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].qckcDcsl;
        var bqrkDcsl = this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].bqrkDcsl;
        var bqckFysbfDcsl = this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].bqckFysbfDcsl;
        var qmkcDcsl = parseFloat(qckcDcsl) + parseFloat(bqrkDcsl) - parseFloat(bqckFysbfDcsl);
        this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].qmkcDcsl = qmkcDcsl.toFixed(2);
        return this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].qmkcDcsl;
      };
    },
    // 3：第17列=第3列+第7列－第10列－第13列
    col17() {
      return function (index) {
        var qckcZgje = this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].qckcZgje;
        var bqrkZgje = this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].bqrkZgje;
        var bqckYsbfje = this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].bqckYsbfje;
        var bqckFysbfZyje = this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].bqckFysbfZyje;
        var qmkcZgje = parseFloat(qckcZgje) + parseFloat(bqrkZgje) - parseFloat(bqckYsbfje) - parseFloat(bqckFysbfZyje);
        this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].qmkcZgje = qmkcZgje.toFixed(2);
        return this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].qmkcZgje;
      };
    },
    getYPXH() {
      return function (index, itemO) {
        // var ypxh = itemO.dmmc;
        // return this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO[index].ypxh;
        return itemO.dmmc;
      };
    }
  },
  //子页面调用父页面方法
  watch: {
    // 监听数据变化
    "tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO": {
      handler(newVal, oldVal) {
        var _self = this;
        _self.totalRows(
          "cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO",
          "otherData.cpygxcqkmxbGridlbVOhj"
        );
      },
      deep: true
    }
  },
  created: function () {
    this.initializationData = this.getSelectSbbInfo(); // 初始化 取值
    this.initializationDataAssignment(); // 初始化表内数据 赋值
    this.inTableVerification(); // 表内校验
    this.betweenTables(); // 表间数据处理
    this.initializationOfPublicServices()  //  暂时替换header节点
  },
  mounted: function () {
    this.keyboardSwitchingCell("taxTable", "text"); // 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
    //this.getPdf(this.name,true);
  },
  methods: {
    // 初始化表内数据
    initializationDataAssignment: function () {
      this.tableData.pageData.sbbheadVO.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh    //纳税人识别号
      this.tableData.pageData.sbbheadVO.nsrmc = this.initializationData.init.jbxx[0].nsrmc     //纳税人名称
      this.tableData.pageData.sbbheadVO.skssqq = this.tableData.header.sksssjq//税款所属期起
      this.tableData.pageData.sbbheadVO.skssqz = this.tableData.header.sksssjz//税款所属期止
      this.tableData.pageData.otherData.tbrq = this.initializationData.openTableData.openTableDate  //填表日期

    },
    // 表间数据处理
    betweenTables: function () {
      this.tableData.pageData.otherData.ypxhOption = this.initializationData.init.dmlb.dmb_ypxh;
    },
    //初始化表内校验
    inTableVerification: function () {
      this.validationValue.push({
        type: 'fun',
        name: 'ypxh',
        function: this.isNullJsxz
      });
    },
    isNullJsxz: function () {
      var cpygxcqkmxbGridlbVO = this.tableData.pageData.cpygxcqkmxbGrid.cpygxcqkmxbGridlbVO;
      if (this.isNull(cpygxcqkmxbGridlbVO, 'ypxh') != false) {
        return true;
      } else {
        return `<div style="color:red" class="errorDiv">油品型号不能为空</div><br>`;
      }
    },
    // 方法：不能为空
    isNull: function (arr, key) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i][key] == '' || arr[i][key] == null) {
          return false;
        }
      }
    },
  },
  provide() {
    return {
      resetData: this.resetData
    };
  }
};
</script>

