<template>
  <button class="btn" @click="addData($event)">添加</button>

</template>
<script>
  export default {
    name: "addBtn",
    props: ['parentListData','parentListKey',],
    data: function(){
      return {
        tableData: this.parentListData,
        key:this.parentListKey
      }
    },
    methods:{
      // 添加数据
      addData (e) {
        var regSz=RegExp(/^(?:0|\-?(?:0\.\d*[1-9]|[1-9]\d*(?:\.\d*[1-9])?))$/);
        var _self = this;
        var arrBackup = _self.strToFun("otherData"+_self.key.substring(_self.key.lastIndexOf("."),_self.key.length)+"[0]");
        var arr = _self.strToFun(_self.key);
        var obj = arr[0];
        var newObj = {};
        for(var i in obj ){
          if(i=="ewbhxh"){
            var a = arr[arr.length-1].ewbhxh;
            if(a.indexOf("00")>-1){
              var b = a.replace("00","");
              if(parseFloat(b)<10){
                newObj.ewbhxh = "00"+(parseFloat(b)+1)
              }else if(parseFloat(b)>10&&parseFloat(b)<100){
                newObj.ewbhxh = "0"+(parseFloat(b)+1)
              }else{
                newObj.ewbhxh = (parseFloat(b)+1)
              }
            }else{
              newObj.ewbhxh=(parseFloat(arr[arr.length-1].ewbhxh)+1).toString();
            }
          }else{
            newObj[i]=arrBackup[i];
          }
        }
        arr.push(newObj);
        console.log(arr)
        this.$EventBus.$emit("sendpar", {
          sendpar:this.tableData,
        });
        this.$nextTick(function(){
          /*DOM更新了*/
          if($("#taxTable").attr('freezeRowNum')||$("#taxTable").attr('freezeColumnNum')){
            _self.tool.analyzingFixedColumns();
          }
        })
      }
    },
  }
</script>
<style scoped>
  .btn {
    width: 45px;
    margin-right: 4px;
    float: left;
  }
</style>



