  computed: {
    // 一、减税项目 栏次序号
    ewbhxhJSXM() {
      return function (index) {
        var ewbhxh = parseFloat(index) + 2;
        this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].ewbhxh = ewbhxh.toString();
        return this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].ewbhxh;
      }
    },
    // 二、免税项目 栏次序号
    ewbhxhMSXM() {
      return function (index) {
        var length = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO.length;
        var ewbhxh = parseFloat(length) + parseFloat(index) + 5;
        this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[index].ewbhxh = ewbhxh.toString();
        return this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[index].ewbhxh;
      }
    },
    totalNum() {
      var num = parseFloat(this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO.length) + 2;
      this.tableData.pageData.otherData.total = num.toString();
      return this.tableData.pageData.otherData.total;
    },
    // 出口免税 栏次
    // 跨境服务 栏次
    ckmsXhNum() {
      return function (num, key) {
        var length = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO.length;
        var lc = parseFloat(length) + parseFloat(num);
        this.tableData.pageData.otherData[key] = lc.toString();
        return this.tableData.pageData.otherData[key];
      }
    },
    // 一、减税项目 本期应抵减税额 3=1+2 bqydjse=qcye+bqfse
    jsxmColumn3() {
      return function (index) {
        var qcye = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].qcye;
        var bqfse = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqfse;
        var result = parseFloat(qcye) + parseFloat(bqfse);
        this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqydjse = result.toFixed(2);
        return this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqydjse;
      };
    },
    // 一、减税项目 期末余额 5=3-4 qmye=bqydjse-bqsjdjse
    jsxmColumn5() {
      return function (index) {
        var bqydjse = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqydjse;
        var bqsjdjse = this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].bqsjdjse;

        var result = parseFloat(bqydjse) - parseFloat(bqsjdjse);
        this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].qmye = result.toFixed(2);
        return this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[index].qmye;
      };
    },

    // 二、免税项目 扣除后免税销售额 3=1-2 kchmsxse=mzzzsxmxse-bqsjkcje
    msxmColumn3() {
      return function (index) {
        var mzzzsxmxse = this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[index].mzzzsxmxse;
        var bqsjkcje = this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[index].bqsjkcje;

