    getCurrentRow: function (val) {
      console.log('val', val);
    },
    sureChoose: function (type) {
      if (type == "JSXM") {
        console.log('this.checkedJSXM', this.checkedJSXM);
        if (this.checkedJSXM != '' || this.checkedJSXM != null) {
          this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[this.jsxmIndex].hmc = this.checkedJSXM.dm + ' ' + this.checkedJSXM.mc;
          this.tableData.pageData.zzsjmssbmxbjsxmGrid.zzsjmssbmxbjsxmGridlbVO[this.jsxmIndex].swsxDm = this.checkedJSXM.swsxDm;
        }
        this.modalJSXM.isShow = false;
        this.jsxmIndex = '';
        // this.checkedJSXM = '';
      }
      if (type == "MSXM") {
        if (this.checkedMSXM != '' || this.checkedMSXM != null) {
          this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[this.msxmIndex].hmc = this.checkedMSXM.dm + ' ' + this.checkedMSXM.mc;
          this.tableData.pageData.zzsjmssbmxbmsxmGrid.zzsjmssbmxbmsxmGridlbVO[this.msxmIndex].swsxDm = this.checkedMSXM.swsxDm;
        }
        this.modalMSXM.isShow = false;
        this.msxmIndex = '';
        // this.checkedMSXM = '';
      }
    }
  },
  provide() {
    //子页面调用父页面方法
    return {
      resetData: this.resetData
    };
  }
};
</script>

<style scoped>
.tdBorder th {
  border: 1px solid #333 !important;
}
.cancelBtn {
  margin-top: 20px;
}
.el-table__body{
  width: 100%
}
</style>




