<template>
  <button class="btn" @click="deleteData($event)">删除</button>
</template>
<script>
  export default {
    name: "deleteBtn",
    props: ['parentListData','parentListKey'],
    data: function(){
      return {
        tableData: this.parentListData,
        key:this.parentListKey
      }
    },
    methods:{
      deleteData (e) {
        var _self = this;
        this.strToFun(this.key).splice(e.target.dataset.num,1);
        this.$nextTick(function(){
          /*DOM更新了*/
          if($("#taxTable").attr('freezeRowNum')||$("#taxTable").attr('freezeColumnNum')){
            _self.tool.analyzingFixedColumns();
          }
        })
      },
    }
  }
</script>
<style scoped>
  .btn {
    width: 45px;
    margin-right: 4px;
  }
</style>



