           var hjhj = this.tableData.pageData.otherData.trGridlbVOhj.hjhj
           var zbList = this.flzlbsj1.pageData.zzsxgmGrid.zzsxgmGridlb;
           var zbListHj = parseFloat(zbList[0].bqybtse)+parseFloat(zbList[1].bqybtse);
           if(parseFloat(hjhj)!= parseFloat(zbListHj)){
            return `<div style="color:red" data-id="hjhj" class="errorDiv">本次应纳税额_增值税_合计应应等于《增值税主表》栏次24_货物及劳务_本月数+栏次24_服务、不动产和无形资产_本月数。</div><br>`
           }else{
             return true;
           }
         },

    	// 分支机构纳税人识别号
    	isNullFzjgnsrsbh: function(){
	      var listtqyzb = this.tableData.pageData.body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO
	       for(var i=0;i<listtqyzb.length;i++){
          if(listtqyzb[i].fzjgnsrsbh==""||listtqyzb[i].fzjgnsrsbh==null){
            return `<div style="color:red" data-id="fzjgnsrsbh`+i+`" class="errorDiv">分支机构纳税人识别号为必填项。</div><br>`
          }else{
            if(i==listtqyzb.length-1){
              return true;
            }
          }
        }
	    },
	    //分支机构名称
	    isNullFzjgmc: function(){
	      var listtqyzb = this.tableData.pageData.body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO
	      for(var i=0;i<listtqyzb.length;i++){
          if(listtqyzb[i].fzjgmc==""||listtqyzb[i].fzjgmc==null){
            return `<div style="color:red" data-id="fzjgmc`+i+`" class="errorDiv">分支机构名称为必填项。</div><br>`
          }else{
            if(i==listtqyzb.length-1){
              return true;
            }
          }
	      }
	    },
	    //坐落区
	    isNullZlqx: function(){
	      var listtqyzb = this.tableData.pageData.body.tynsyhbxxtqyzGrid.tynsyhbxxtqyzGridlbVO
	      for(var i=0;i<listtqyzb.length;i++){
          if(listtqyzb[i].zlqx==""||listtqyzb[i].zlqx==null){
            return `<div style="color:red" data-id="zlqx`+i+`" class="errorDiv">坐落区为必填项。</div><br>`
          }else{
            if(i==listtqyzb.length-1){
              return true;
            }
          }
        }
	    }
	    /****/
    },
    created:function () {
      this.initializationData = this.getSelectSbbInfo();
      this.inTableVerification();  // 表内校验
      this.$route.params.id;
      this.initializationDataAssignment();  //初始化数据
      this.betweenTables();  //表间数据
      this.initializationOfPublicServices()  //  暂时替换header节点
    }
  }
</script>
