      this.initializationData = this.getSelectSbbInfo();
      this.inTableVerification();  // 表内校验
      this.$route.params.id;
      this.initializationDataAssignment();  //初始化数据
      this.betweenTables();  //表间数据
      this.initializationOfPublicServices()  //  暂时替换header节点
    }
  }
</script>

