      var resultCz = parseFloat(sbzjsrhj) + parseFloat(ynsehj) + parseFloat(jmsehj) + parseFloat(xgmjzehj) + parseFloat(yjsehj) + parseFloat(ybtsehj);
      if (resultCj != 0 || resultCz != 0) {
