          return this.tableData.pageData.jmbyqndks;
        },
        // 第9行＝第3行+4行-5行-6行-7行-8行；
        reversedSjlre:function () {
          this.tableData.pageData.sjlre = parseFloat(this.tableData.pageData.lrze)  + parseFloat(this.tableData.pageData.jtdywjsdynsdse) - parseFloat(this.tableData.pageData.jbzssr ) -  parseFloat(this.tableData.pageData.jmssrjjsrsdjmdyhje)- parseFloat(this.tableData.pageData.jgdzcjszjtje)  - parseFloat(this.tableData.pageData.jmbyqndks)
          return this.tableData.pageData.sjlre;
        },
        //第11行＝第9行*第10行ynsdse
        reversedYnsdse(){
          this.tableData.pageData.ynsdse =  parseFloat(this.tableData.pageData.sjlre)* parseFloat(this.tableData.pageData.sl)* parseFloat(this.checkData.tsclynsbl_jd);
          if(this.checkData.tsclynssde_nd=="10"){
            if(this.tableData.pageData.sjlre>0){
             this.tableData.pageData.ynsdse =  parseFloat(this.tableData.pageData.sjlre)* parseFloat(this.tableData.pageData.sl)* parseFloat(this.checkData.tsclynsbl_jd);
            }else{
              this.tableData.pageData.ynsdse = 0;
            }
          }else if(this.checkData.tsclynssde_nd!="10"){
            if(this.tableData.pageData.sjlre>0){
              this.tableData.pageData.ynsdse = this.tableData.pageData.sjlre*this.tableData.pageData.sl;
            }else{
              this.tableData.pageData.ynsdse = 0;
            }
          }
          return this.tableData.pageData.ynsdse;
        },
        //第12行  减：减免所得税额’＝A201030表第30行‘合计’*‘特殊处理应纳税比例’
        reversedJjmsdse(){
          this.tableData.pageData.jjmsdse = this.checkData.A201030_hj * this.checkData.tsclynsbl_jd;
          if(this.checkData.tsclynssde_nd=="10"){
            this.tableData.pageData.jjmsdse = this.checkData.A201030_hj * this.checkData.tsclynsbl_jd;
          }else{
            this.tableData.pageData.jjmsdse = this.checkData.A201030_hj;
          }
          return this.tableData.pageData.jjmsdse;
        },
        //第十三行：上期的(第13行+第15行)（新表样）;
        reversedJsjyjnsdse(){
          this.tableData.pageData.jsjyjnsdse = this.checkData.A13+this.checkData.A15;
          return this.tableData.pageData.jsjyjnsdse;
        },
        //第十五行
        reversedBqybsdse(){
          this.tableData.pageData.bqybsdse = parseFloat(this.tableData.pageData.ynsdse)- parseFloat(this.tableData.pageData.jjmsdse)- parseFloat(this.tableData.pageData.jsjyjnsdse)- parseFloat(this.tableData.pageData.jtdywyjsdse);
          if(this.checkData.tsclynssde_nd=="06"){
            if((parseFloat(this.tableData.pageData.ynsdse)- parseFloat(this.tableData.pageData.jjmsdse))*parseFloat(this.checkData.tsclynsbl_jd)-parseFloat(this.tableData.pageData.jsjyjnsdse)-parseFloat(this.tableData.pageData.jtdywyjsdse)>0){
              this.tableData.pageData.bqybsdse = ( parseFloat(this.tableData.pageData.ynsdse)-parseFloat(this.tableData.pageData.jjmsdse))* parseFloat(this.checkData.tsclynsbl_jd)-parseFloat(this.tableData.pageData.jsjyjnsdse)- parseFloat(this.tableData.pageData.jtdywyjsdse);
            }else{
              this.tableData.pageData.bqybsdse=0;
            }
          }else{
            if(parseFloat(this.tableData.pageData.ynsdse)- parseFloat(this.tableData.pageData.jjmsdse)-parseFloat(this.tableData.pageData.jsjyjnsdse)-parseFloat(this.tableData.pageData.jtdywyjsdse)>0){
              this.tableData.pageData.bqybsdse = parseFloat(this.tableData.pageData.ynsdse)- parseFloat(this.tableData.pageData.jjmsdse)- parseFloat(this.tableData.pageData.jsjyjnsdse)- parseFloat(this.tableData.pageData.jtdywyjsdse);
            }else{
              this.tableData.pageData.bqybsdse=0;
            }
          }
          return this.tableData.pageData.bqybsdse;
        },
        // 第16行＝第17行+18行+19行
        reversedZjgbqftybsdse(){
          this.tableData.pageData.zjgbqftybsdse = parseFloat(this.tableData.pageData.zjgftybsdse )+ parseFloat(this.tableData.pageData.czjzfpybsdse) + parseFloat(this.tableData.pageData.zjgjyztscjyznbmftbl);
          return this.tableData.pageData.zjgbqftybsdse;
        },
        //总机构上年是否为小微’Y，第17行＝第15行  总机构上年是否为小微’N，第17行＝第15行*‘总机构分摊比例’
        reversedZjgftybsdse(){
          this.tableData.pageData.zjgftybsdse = parseFloat(this.tableData.pageData.bqybsdse) * parseFloat(this.tableData.pageData.zjgftbl);
          if(this.checkData.isxwxbqybzzjgfszy=="Y"){
            this.tableData.pageData.zjgftybsdse = this.tableData.pageData.bqybsdse;
          }
          if(this.checkData.isxwxbqybzzjgfszy=="N"){
            this.tableData.pageData.zjgftybsdse = parseFloat(this.tableData.pageData.bqybsdse) * parseFloat(this.tableData.pageData.zjgftbl);
          }
          return this.tableData.pageData.zjgftybsdse;
        },
        //‘总机构上年是否为小微’Y，第18行＝0‘总机构上年是否为小微’N，第18行＝第15行*‘财政集中分配比例’
        reversedCzjzfpybsdse(){
          this.tableData.pageData.czjzfpybsdse = parseFloat(this.tableData.pageData.bqybsdse)  * parseFloat(this.tableData.pageData.czjzfpbl);
          if(this.checkData.isxwxbqybzzjgfszy=="Y"){
            this.tableData.pageData.czjzfpybsdse = 0;
          }
          if(this.checkData.isxwxbqybzzjgfszy=="N"){
            this.tableData.pageData.czjzfpybsdse = parseFloat(this.tableData.pageData.bqybsdse)  * parseFloat(this.tableData.pageData.czjzfpbl);
          }
          return this.tableData.pageData.czjzfpybsdse;
        },
        //总机构上年是否为小微’Y，第19行＝0‘总机构上年是否为小微’N，第19行＝第15行*‘全部分支机构分摊比例’*‘总机构具有主体生产经营职能部门分摊比例’
        reversedZjgjyztscjyzndbmftsdse(){
          this.tableData.pageData.zjgjyztscjyzndbmftsdse = parseFloat(this.tableData.pageData.bqybsdse)  * parseFloat(this.tableData.pageData.qbfzjgftbl)* parseFloat(this.tableData.pageData.zjgjyztscjyznbmftbl);
          if(this.checkData.isxwxbqybzzjgfszy=="Y"){
            this.tableData.pageData.zjgjyztscjyzndbmftsdse = 0;
          }
          if(this.checkData.isxwxbqybzzjgfszy=="N"){
            this.tableData.pageData.zjgjyztscjyzndbmftsdse = parseFloat(this.tableData.pageData.bqybsdse)  * parseFloat(this.tableData.pageData.qbfzjgftbl)* parseFloat(this.tableData.pageData.zjgjyztscjyznbmftbl);
          }
          return this.tableData.pageData.zjgjyztscjyzndbmftsdse;
        }
      },
      mounted: function () {
        this.keyboardSwitchingCell("taxTable","text");// 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
        //this.getPdf(this.name,true);
      },
      created:function () {
        this.certificateDictionary = this.getDict(this.jsonUrl+"/static/json/dict/certificateDict.json");//获取身份字典表
        this.cityDictionary = this.getDict(this.jsonUrl+"/static/json/dict/cityDict.json");//获取城市字典表
        this.initializationData = this.getDict(this.jsonUrl+"/static/json/initializationData.json");//获取城市字典表
        //this.getInitializationAndSaveInfo({},this.batchData); // 获取初始化数据和保存的数据
        this.inTableVerification();// 表内校验
        //this.automaticRecode();
        console.log(this.getUrlParam("type"))

        this.resetData=JSON.parse(JSON.stringify(this.tableData)); //this.templateData是父组件传递的对象
        // if(this.getUrlParam("type")=="branch"){
        //  this.getPdf(this.name,true);
       // }
      }
    }
</script>

