<template>
  <el-container>
    <el-header>
      <app-header :parentData="tableData" :parentValidation="validationValue" :parentPdfName="name"></app-header>
    </el-header>
    <el-container>
      <el-main id="allTable">
        <el-scrollbar style="height: 100%;">
          <div style="padding: 20px ;background-color: #fff" id="pdfDom" name="pdfDomBranch" class="pdfDomBranch">
            <table cellspacing="0" cellpadding="0" width="696" height="365" id="taxTable">
              <tr>
                <td width="630" height="46" align="center" valign="middle" colspan="4" class="noBorder">
                  <B>部分产品销售统计表</B>
                </td>
              </tr>
              <tr>
                <td width="630" height="22" align="left" valign="middle" colspan="4" class="noBorder">
                  税款所属时间：
                  <span v-format-date v-html="tableData.pageData.bfcpxstjbForm.skssqq"></span>
                  至
                  <span v-format-date v-html="tableData.pageData.bfcpxstjbForm.skssqz"></span>
                </td>
              </tr>
              <tr>
                <td width="630" height="22" align="left" valign="middle" colspan="4" class="noBorder">
                  纳税人识别号：
                  <span v-html="tableData.pageData.bfcpxstjbForm.nsrsbh"></span>
                </td>
              </tr>
              <tr>
                <td width="630" height="22" align="left" valign="middle" colspan="4" class="noBorder">
                  纳税人名称（公章）：
                  <span v-html="tableData.pageData.bfcpxstjbForm.nsrmc"></span>
                </td>
              </tr>
              <tr>
                <td width="455" height="22" align="left" valign="middle" colspan="3" class="noBorder">
                  填表日期：
                  <span  v-format-date v-html="tableData.pageData.bfcpxstjbForm.tbrq"></span>
                </td>
                <td width="175" height="22" align="right" valign="middle" class="noBorder">金额单位：元（列至角分）</td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="208" height="33" align="center" valign="middle" style="padding:0;background-image:url('../../../../static/images/81_line.png');background-repeat:no-repeat;position:relative;background-size:100% 100%;image-rendering: -webkit-optimize-contrast;">
                  <span style="position:absolute;right:80px;top:2px">项目</span>
                  <span style="position:absolute;left:15px;bottom:5px">产品名称</span>
                </td>
                <td class="uneditable-bgColor" width="90" height="33" align="center" valign="middle">栏次</td>
                <td class="uneditable-bgColor" width="157" height="33" align="center" valign="middle">销售数量</td>
                <td class="uneditable-bgColor" width="175" height="33" align="center" valign="middle">销售额</td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="208" height="22" align="left" valign="middle">一、轮 胎</td>
                <td class="uneditable-bgColor" width="90" height="22" align="center" valign="middle">1=2+3</td>
                <td class="font-color-red" width="157" height="22" align="right" valign="middle" v-floatOld="2" data-id="ltxssl" v-html="tableData.pageData.bfcpxstjbForm.ltxssl" :text="row1('ltxssl','zwxltxssl','xjltxssl')"></td>
                <td class="font-color-red" width="175" height="22" align="right" valign="middle" v-floatOld="2" data-id="ltxse" v-html="tableData.pageData.bfcpxstjbForm.ltxse" :text="row1('ltxse','zwxltxse','xjltxse')"></td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="208" height="22" align="left" valign="middle">其中：子午线轮胎</td>
                <td class="uneditable-bgColor" width="90" height="22" align="center" valign="middle">2</td>
                <td width="157" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='2911'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="zwxltxssl" v-model="tableData.pageData.bfcpxstjbForm.zwxltxssl" :disabled="tableData.pageData.otherData.hymxdm=='2911'"/>
                </td>
                <td width="175" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='2911'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="zwxltxse" v-model="tableData.pageData.bfcpxstjbForm.zwxltxse" :disabled="tableData.pageData.otherData.hymxdm=='2911'"/>
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="208" height="22" align="center" valign="middle">斜交轮胎</td>
                <td class="uneditable-bgColor" width="90" height="22" align="center" valign="middle">3</td>
                <td width="157" height="22" align="right" valign="middle"  :class="tableData.pageData.otherData.hymxdm=='2911'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="xjltxssl" v-model="tableData.pageData.bfcpxstjbForm.xjltxssl" :disabled="tableData.pageData.otherData.hymxdm=='2911'"/>
                </td>
                <td width="175" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='2911'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="xjltxse" v-model="tableData.pageData.bfcpxstjbForm.xjltxse" :disabled="tableData.pageData.otherData.hymxdm=='2911'"/>
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="208" height="22" align="left" valign="middle">二、酒 精</td>
                <td class="uneditable-bgColor" width="90" height="22" align="center" valign="middle">4=5+6+7</td>
                <td class="font-color-red" width="157" height="22" align="right" valign="middle" v-floatOld="2" data-id="jjxssl" v-html="tableData.pageData.bfcpxstjbForm.jjxssl" :text="row4('jjxssl','yyycqydjjxssl','syjjxssl','qtjjxssl')"></td>
                <td class="font-color-red" width="175" height="22" align="right" valign="middle" v-floatOld="2" data-id="jjxse" v-html="tableData.pageData.bfcpxstjbForm.jjxse" :text="row4('jjxse','yyycqydjjxse','syjjxse','qtjjxse')"></td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="208" height="22" align="left" valign="middle">其中：用于乙醇汽油的酒精</td>
                <td class="uneditable-bgColor" width="90" height="22" align="center" valign="middle">5</td>
                <td width="157" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='1511'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="yyycqydjjxssl" v-model="tableData.pageData.bfcpxstjbForm.yyycqydjjxssl" :disabled="tableData.pageData.otherData.hymxdm=='1511'"/>
                </td>
                <td width="175" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='1511'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="yyycqydjjxse" v-model="tableData.pageData.bfcpxstjbForm.yyycqydjjxse"  :disabled="tableData.pageData.otherData.hymxdm=='1511'"/>
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="208" height="22" align="center" valign="middle">食用酒精</td>
                <td class="uneditable-bgColor" width="90" height="22" align="center" valign="middle">6</td>
                <td width="157" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='1511'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="syjjxssl" v-model="tableData.pageData.bfcpxstjbForm.syjjxssl"  :disabled="tableData.pageData.otherData.hymxdm=='1511'"/>
                </td>
                <td width="175" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='1511'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="syjjxse" v-model="tableData.pageData.bfcpxstjbForm.syjjxse"  :disabled="tableData.pageData.otherData.hymxdm=='1511'"/>
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="208" height="22" align="center" valign="middle">其他酒精</td>
                <td class="uneditable-bgColor" width="90" height="22" align="center" valign="middle">7</td>
                <td width="157" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='1511'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="qtjjxssl" v-model="tableData.pageData.bfcpxstjbForm.qtjjxssl"  :disabled="tableData.pageData.otherData.hymxdm=='1511'"/>
                </td>
                <td width="175" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='1511'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="qtjjxse" v-model="tableData.pageData.bfcpxstjbForm.qtjjxse"  :disabled="tableData.pageData.otherData.hymxdm=='1511'"/>
                </td>
              </tr>
              <tr>
                <td class="uneditable-bgColor" width="208" height="22" align="left" valign="middle">三、摩托车（排量&lt;250毫升）</td>
                <td class="uneditable-bgColor" width="90" height="22" align="center" valign="middle">8</td>
                <td width="157" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='3751'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="mtcxssl" v-model="tableData.pageData.bfcpxstjbForm.mtcxssl" :disabled="tableData.pageData.otherData.hymxdm=='3751'"/>
                </td>
                <td width="175" height="22" align="right" valign="middle" :class="tableData.pageData.otherData.hymxdm=='3751'?'':'editable-bgColor'">
                  <input type="text" class="input-border-bgColor" v-floatOld="2" data-id="mtcxse" v-model="tableData.pageData.bfcpxstjbForm.mtcxse" :disabled="tableData.pageData.otherData.hymxdm=='3751'"/>
                </td>
              </tr>
            </table>
          </div>
        </el-scrollbar>
      </el-main>
      <el-aside class="bar">
        <informationBar :parentName="name"></informationBar>
      </el-aside>
    </el-container>
  </el-container>
</template>

<script>
/**
 * @Author lmz
 * @Description 部分产品销售统计表
 * @Date
 **/
import Header from "../../app-header";
import informationBar from "../../informationBar.vue";
import router from "../../../router/index.js";
import addBtn from "../../addBtn.vue";
import deleteBtn from "../../deleteBtn.vue";

var linq = require("linq");
export default {
  name: "10101_021_10",
  components: {
    "app-header": Header,
    informationBar: informationBar,
    addBtn: addBtn,
    deleteBtn: deleteBtn
  },
  props: ["routerParam"],
  data: function () {
    return {
      certificateDictionary: "", //身份字典
      cityDictionary: "",
      validationValue: [], //校验规则
      requiredItems: [], //必填项目
      name: "10101_021_10", //文件名称
      mbyqndks: "", //弥补以前年度亏损
      initializationData: "", //初始化数据
      saveInfo: "", //已保存过的数据
      tableData: {
        header: {
          zsxmDm:"10101",// 征收项目代码
          bblxDm:"10101",// 报表类型代码
          bbDm:"10101_021",// 报表代码
          sksssjq:"20190401",// 税款所属时间起
          sksssjz:"20190430",// 税款所属时间止
          saveUserId:"949407",// 保存用户ID
          nsrsbh:"91120102553420400T",// 纳税人识别号 (追加参数)
          djxh:"10111242000025815982",// 登记序号
          pcDm:"0",// 多批次代码（不是多批次填0，是多批次从1开始计算）
          multiPatch:"N",// 按批申报，Y：是；N：不是
          nsqxdm:"06",// 纳税期限代码
          nsrsbq:"201905",// 纳税申报期
          versionNumber:"10" //版本号（报表属于那个版本）
        },
        pageData: {
          bfcpxstjbForm: {
            nsrsbh: "", //纳税人识别号
            nsrmc: "", //纳税人名称
            skssqq: "", //税款属期起日
            skssqz: "", //税款属期止日
            tbrq: "", //填报日期
            ltxssl: "0.00", //1轮胎销售数量
            ltxse: "0.00", //1轮胎销售额
            zwxltxssl: "0.00", //2子午线轮胎销售数量
            zwxltxse: "0.00", //2子午线轮胎销售数额
            xjltxssl: "0.00", //3斜交轮胎销售数量
            xjltxse: "0.00", //3斜交轮胎销售额
            jjxssl: "0.00", //4酒精销售数量
            jjxse: "0.00", //4酒精销售额
            yyycqydjjxssl: "0.00", //5用于乙醇汽油的酒精销售数量
            yyycqydjjxse: "0.00", //5用于乙醇汽油的酒精销售额
            syjjxssl: "0.00", //6食用酒精销售数量
            syjjxse: "0.00", //6食用酒精销售额
            qtjjxssl: "0.00", //7其他酒精销售数量
            qtjjxse: "0.00", //7其他酒精销售额
            mtcxssl: "0.00", //8摩托车销售数量
            mtcxse: "0.00" //8摩托车销售额
          },
          checkData: {
            // 表间数据的校验
          },
          otherData: {
            nsrmc: "纳税人名称1", //纳税人名称
            nsrsbh: "纳税人识别号1", //纳税人识别号
            skssqq: "2019-08-01", //税款所属期起
            skssqz: "2019-08-31", //税款所属期止
            tbrq: "2019-08-12", //填表日期
            hymxdm:''//行业明细代码
          }
        }
      }
    };
  },
  computed: {
    // 1：第1行=第2行+第3行
    row1(){
      return function (row1,row2,row3) {
        var val2 = this.tableData.pageData.bfcpxstjbForm[row2];
        var val3 = this.tableData.pageData.bfcpxstjbForm[row3];
        var result = parseFloat(val2) + parseFloat(val3);
        this.tableData.pageData.bfcpxstjbForm[row1] = result.toFixed(2);
        return this.tableData.pageData.bfcpxstjbForm[row1];
      }; 
    },
    // 2：第4行=第5行+第6行+第7行
    row4(){
      return function (row4,row5,row6,row7) {
        var val5 = this.tableData.pageData.bfcpxstjbForm[row5];
        var val6 = this.tableData.pageData.bfcpxstjbForm[row6];
        var val7 = this.tableData.pageData.bfcpxstjbForm[row7];
        var result = parseFloat(val5) + parseFloat(val6)+ parseFloat(val7);
        this.tableData.pageData.bfcpxstjbForm[row4] = result.toFixed(2);
        return this.tableData.pageData.bfcpxstjbForm[row4];
      }; 
    }
  },
  //子页面调用父页面方法
  watch: {
    // 监听数据变化
  },
  created: function () {
    this.initializationData = this.getSelectSbbInfo(); // 初始化 取值
    this.initializationDataAssignment(); // 初始化表内数据 赋值
    this.inTableVerification(); // 表内校验
    this.betweenTables(); // 表间数据处理
    this.initializationOfPublicServices()  //  暂时替换header节点

  },
  mounted: function () {
    this.keyboardSwitchingCell("taxTable", "text"); // 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
    //this.getPdf(this.name,true);
  },
  methods: {
    // 初始化表内数据
    initializationDataAssignment: function () {
    console.log('this.initializationData.data[0].sksssjq',this.initializationData.data[0].sksssjq)
      this.tableData.pageData.bfcpxstjbForm.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh; //纳税人识别号
      this.tableData.pageData.bfcpxstjbForm.nsrmc = this.initializationData.init.jbxx[0].nsrmc; //纳税人名称
      this.tableData.pageData.bfcpxstjbForm.skssqq = this.initializationData.data[0].header.sksssjq; //税款所属期起
      this.tableData.pageData.bfcpxstjbForm.skssqz = this.initializationData.data[0].header.sksssjz; //税款所属期止
      this.tableData.pageData.bfcpxstjbForm.tbrq = this.initializationData.openTableData.openTableDate  //填表日期
      
      // 行业明细代码 HYMX_DM
      this.tableData.pageData.otherData.hymxdm = linq.from(this.initializationData.init.wsxx).where("item=>item.code=='HYMX_DM'").defaultIfEmpty([]).toArray()[0].value;
      console.log('HYMX_DM',this.tableData.pageData.otherData.hymxdm);
    },
    // 表间数据处理
    betweenTables: function () { },
    //初始化表内校验
    inTableVerification: function () { }
  },
  provide() {
    return {
      resetData: this.resetData
    };
  }
};
</script>

<style scoped>
</style>




