         // this.retainValidDecimal('otherData.bqjxsemxbGridlbVOhj['+kayNamehj+']','2');
          return trGridlbVO[kayNamehj];
        }
      }

    },
    methods: {//重置页面数据--初始化数据和隐藏列数据不重置
      resetData:function(){

      },
      //初始化表内校验
      inTableVerification:function(){
        //trccfhdncpzzsjxseGridlbVO是个数组
        this.validationValue.push({type:"fun",name:"gjncpyyscjyqbgchwsthdncpzzsjxsejsbGrid.gjncpyyscjyqbgchwsthdncpzzsjxsejsbGridlbVO",function:this.isNullcpmc}),
          this.validationValue.push({type:"fun",name:"gjncpyyscjyqbgchwsthdncpzzsjxsejsbGrid.gjncpyyscjyqbgchwsthdncpzzsjxsejsbGridlbVO",function:this.isNullhyncpmc})
      },
      // “产品名称”不能为空！！
      isNullcpmc: function(){
        var trccfhdncpzzsjxseGridlbVO = this.tableData.pageData.gjncpyyscjyqbgchwsthdncpzzsjxsejsbGrid.gjncpyyscjyqbgchwsthdncpzzsjxsejsbGridlbVO;
        if(this.isNull(trccfhdncpzzsjxseGridlbVO,'cpmc1')==undefined){
          return true
        }else{
          return `<div style="color:red" class="errorDiv">产品名称为必填项。</div><br>`
        }
      },
      //“耗用农产品名称”不能为空！！
      isNullhyncpmc: function(){
        var trccfhdncpzzsjxseGridlbVO = this.tableData.pageData.gjncpyyscjyqbgchwsthdncpzzsjxsejsbGrid.gjncpyyscjyqbgchwsthdncpzzsjxsejsbGridlbVO;
        if(this.isNull(trccfhdncpzzsjxseGridlbVO,' hyncpmc')==undefined){
          return true
        }else{
          return `<div style="color:red" class="errorDiv">耗用农产品名称为必填项。</div><br>`
        }
      },
      //select下拉
      selectVal:function(index){
        var kclVal = this.tableData.pageData.checkData.kclVal[index];
        console.log(kclVal);
      }

    },
    created:function () {
      this.inTableVerification();
      this.$route.params.id;
      this.tool.analyzingFixedColumns()   // 固定行
      //this.initializationData = this.getSelectSbbInfo(this.routerParam);
      this.tableData.pageData.checkData.kclList=this.getDict(this.jsonUrl+"/static/json/dict/houseAdvantage");

    }
  }
</script>
