<template>
  <el-container>
    <el-header>
      <app-header :parentData="tableData" :parentValidation="validationValue" :parentPdfName="name"></app-header>
    </el-header>
    <el-container>
      <el-main id="allTable">
        <el-scrollbar style="height: 100%;">
          <div id="pdfDom" name="pdfDomBranch" class="pdfDomBranch">
            <table cellspacing="0" cellpadding="0" width="987" height="274" id="taxTable">
              <tr>
                <td width="955" height="46" align="center" valign="middle" colspan="6" class="noBorder">
                  <B>成本法核定农产品增值税进项税额计算表</B>
                </td>
              </tr>
              <tr>
                <td width="955" height="22" align="center" valign="middle" colspan="6" class="noBorder">税款所属时间： <span v-html="tableData.pageData.sbbhead.skssqq"></span>至<span v-html="tableData.pageData.sbbhead.skssqz"></span></td>
              </tr>
              <tr>
                <td width="635" height="22" align="left" valign="middle" colspan="4" class="noBorder">纳税人名称（公章）：<span v-html="this.tableData.pageData.sbbhead.nsrmc"></span></td>
                <td width="320" height="22" align="left" valign="middle" colspan="2" class="noBorder">纳税人识别号： <span v-html="this.tableData.pageData.sbbhead.nsrsbh"></span></td>
              </tr>
              <tr>
                <td width="45" height="52" align="center" valign="middle" rowspan="2" class="uneditable-bgColor">序号</td>
                <td width="270" height="52" align="center" valign="middle" rowspan="2" class="uneditable-bgColor">产品名称</td>
                <td width="160" height="30" align="center" valign="middle" class="uneditable-bgColor">扣除率（%）</td>
                <td width="160" height="30" align="center" valign="middle" class="uneditable-bgColor">当期主营业务成本（元）</td>
                <td width="160" height="30" align="center" valign="middle" class="uneditable-bgColor">农产品耗用率（%）</td>
                <td width="160" height="30" align="center" valign="middle" class="uneditable-bgColor">当期允许抵扣农产品进项税额（元）</td>
              </tr>
              <tr>
                <td width="160" height="22" align="center" valign="middle"  class="uneditable-bgColor">L1</td>
                <td width="160" height="22" align="center" valign="middle"  class="uneditable-bgColor">L2</td>
                <td width="160" height="22" align="center" valign="middle"  class="uneditable-bgColor">L3</td>
                <td width="160" height="22" align="center" valign="middle"  class="uneditable-bgColor">L4=L2*L3*L1/（1+L1）</td>
              </tr>
              <tr v-for="(item,index) in tableData.pageData.zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO">
                <td width="45" height="22" align="center" valign="middle"  v-html="item.ewbhxh"></td>
                <td width="270" height="22" align="center" valign="middle" class="editable-bgColor" >
                  <input type="text" v-model="item.cpmc1"  @blur="retainValidDecimal('zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO['+index+'].cpmc1','2')">
                </td>
                <td width="160" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input type="text" v-model="item.kcl"  >
                </td>
                <td width="160" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input type="text" v-model="item.dqzyywcb" @blur="retainValidDecimal('zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO['+index+'].dqzyywcb','2')">
                </td>
                <td width="160" height="22" align="right" valign="middle" class="editable-bgColor">
                  <input type="text" v-model="item.ncphyl" v-percent="4"  >
                </td>
                <td width="160" height="22" align="right" valign="middle" class="font-color-red"  v-html="item.dqyxdkncpjxse" :text="L4Fun(index)" >

                </td>
                <td  width="1160" height="22"  align="left" valign="middle" class="noBorder">
                  <addBtn :data-name="1" :data-num="index"  :parentListData="tableData" :parentListKey="'zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO'" v-show="index==tableData.pageData.zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO.length-1"></addBtn>
                  <deleteBtn :data-name="1" :data-num="index"  :parentListData="tableData" :parentListKey="'zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO'" v-show="index!=0"></deleteBtn>
                </td>
              </tr>
              <tr>
                <td width="795" height="22" align="center" valign="middle" colspan="5" class="uneditable-bgColor">合 计</td>
                <td width="160" height="22" align="right" valign="middle" class="font-color-red" v-html="tableData.pageData.otherData.L9hj.dqyxdkncpjxsehj"></td>
              </tr>
              <tr>
                <td width="955" height="22" align="left" valign="middle" colspan="6" class="noBorder">注：1.采用成本法计算农产品增值税进项税额的试点纳税人填列本表。</td>
              </tr>
              <tr>
                <td width="955" height="22" align="left" valign="middle" colspan="6" class="noBorder">2.对以单一农产品原料生产多种货物或者多种农产品原料生产多种货物的，应分别不同产品填列本表。</td>
              </tr>
              <tr>
                <td width="955" height="22" align="left" valign="middle" colspan="6" class="noBorder">3.各项数据均保留两位小数。</td>
              </tr>
            </table>
          </div>
        </el-scrollbar>
      </el-main>
      <el-aside class="bar">
        <informationBar :parentName="name"></informationBar>
      </el-aside>
    </el-container>
  </el-container>
</template>

<script>
  import Header from "../../app-header";
  import informationBar from "../../informationBar.vue";
  import router from "../../../router/index.js";
  import addBtn from "../../addBtn.vue";
  import deleteBtn from "../../deleteBtn.vue";

  export default {
    name: "10101_015_10",
    watch: { // 监听数据变化
      "tableData.pageData.zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO":{
        handler(newVal, oldVal){
          var _self = this;
          //合计每一列
          _self.totalRows("zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO","otherData.L9hj");
        },
        deep: true
      }
    },
    components: {
      "app-header": Header,
      informationBar: informationBar,
      'addBtn' : addBtn,
      'deleteBtn' : deleteBtn,
    },
    props: ["routerParam"],
    data: function () {
      return {
        certificateDictionary: "", //身份字典
        cityDictionary: "",
        validationValue: [], //校验规则
        requiredItems: [], //必填项目
        name: "10101_015_10", //文件名称
        initializationData: "", //初始化数据
        saveInfo: "", //已保存过的数据
        tableData: {
          header: {
            bbDm: "10101_015_10",
            version: "11",
            zsxmDm: "11",
            bblxDm: "22",
            nsrmc: "",
            nsrsbh: "",
            tbrq: "",
            sksssjz: "",
            sksssjq: ""
          },
          pageData: {  //zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO
            zzsybnsrsb_cbfhdccpzzsjxsejsb:{//成本法核定农产品增值税进项税额计算表
              zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO:[
                {
                  dqyxdkncpjxse:"0.00",    //当期允许抵扣农产品进项税额元 L 4
                  ncphyl:"0.0000%",           //农产品耗用率 L 3
                  dqzyywcb:"0.00",         //当期主营业务成本元    L 2
                  kcl:"16%",              //扣除率  L1
                  cpmc1:"",            //产品名称
                  xh:0  ,                //序号
                  ewbhxh:"1",   //二维序列号
                }
              ]
            },
            sbbhead:{
              nsrsbh:"",   //纳税人识别号
              nsrmc:"",    //纳税人名称
              skssqq:"",   //税款所属期起
              skssqz:""    //税款所属期止
            },

            // 表间数据的校验
            checkData: {
                dqyxdkncpjxsehj:"0.00"
            },
            otherData: {
              L9hj:{
                dqyxdkncpjxsehj:"0.00"
              },
              zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO:[
                {
                  dqyxdkncpjxse:"0.00",    //当期允许抵扣农产品进项税额元 L 4
                  ncphyl:"0.00%",           //农产品耗用率 L 3
                  dqzyywcb:"0.00",         //当期主营业务成本元    L 2
                  kcl:"16%",              //扣除率  L1
                  cpmc1:"",            //产品名称
                  xh:0                   //序号
                }
              ]

            }

          },

        }
      };
    },
    computed: {

        // 1. L4=L2*L3*L1/（1+L1）
      L4Fun(){
        return function (index) {
          var trGridlbVO = this.tableData.pageData.zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO[index];
          var result=""
          if(parseFloat(trGridlbVO.dqzyywcb)*parseFloat(trGridlbVO.ncphyl)+parseFloat(trGridlbVO.kcl)==0 || (parseFloat(trGridlbVO.kcl) + 1) ==0){
            result='0.00';
          }else{
            result = (parseFloat(trGridlbVO.dqzyywcb)*parseFloat(trGridlbVO.ncphyl)+parseFloat(trGridlbVO.kcl))/(parseFloat(trGridlbVO.kcl )+ 1)
            result = result.toFixed(2);
          }
          this.tableData.pageData.zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO[index].dqyxdkncpjxse= result;
          return this.tableData.pageData.zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO[index].dqyxdkncpjxse;
        }
      },
      //2. 合计=L4列之和  监听处理
    },




    created: function () {
      this.initializationData = this.getSelectSbbInfo();
      this.initializationOfPublicServices(); // 初始化公共业务模块
      this.initializationDataAssignment();  //初始化数据赋值
      this.betweenTables();  //表间数据赋值
      this.inTableVerification();//整理校验规则

    },
    mounted: function () {
      this.keyboardSwitchingCell("taxTable", "text"); // 上下左右键盘切换可编辑input框 rbtb：表格id;; text:表單元素類型
      //this.getPdf(this.name,true);

    },
    methods: {
      //初始化数据赋值
      initializationDataAssignment:function () {
        this.tableData.pageData.sbbhead.nsrsbh = this.initializationData.init.jbxx[0].nsrsbh    //纳税人识别号
        this.tableData.pageData.sbbhead.nsrmc =this.initializationData.init.jbxx[0].nsrmc     //纳税人名称

        // this.tableData.pageData.head.tbrq = this.initializationData.openTableData.openTableDate  //填表日期


        this.tableData.pageData.sbbhead.skssqq = this.initializationData.data[0].header.sksssjq;
        this.tableData.pageData.sbbhead.skssqz = this.initializationData.data[0].header.sksssjz;

      },
      //初始化表内校验
      inTableVerification:function(){

        //产品名称不得为空
        this.validationValue.push({type:"fun",name:"zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO",function:this.isNullcpmc}),
         // 0＜L3≤1
        this.validationValue.push({type:"fun",name:"zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO",function:this.L3Fun})
        // 0＜L1≤1
        this.validationValue.push({type:"fun",name:"zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO",function:this.L1Fun})

      },
      // “产品名称”不能为空！！
      isNullcpmc: function(){
        var trccfhdncpzzsjxseGridlbVO = this.tableData.pageData.gjncpyyscjyqbgchwsthdncpzzsjxsejsbGrid.gjncpyyscjyqbgchwsthdncpzzsjxsejsbGridlbVO;
        for (var i=0;i<trccfhdncpzzsjxseGridlbVO.length;i++){
          if(trccfhdncpzzsjxseGridlbVO[i].cpmc1==''|| trccfhdncpzzsjxseGridlbVO[i].cpmc1==null){
            return `<div style="color:red"  data-id="cpmc1`+i+`" class="errorDiv">产品名称为必填项。</div><br>`
          }else{
            if(i==trccfhdncpzzsjxseGridlbVO.length-1){
              return true;
            }
          }
        }
      },
      // 0＜L3≤1
      L3Fun: function(){
        var trccfhdncpzzsjxseGridlbVO = this.tableData.pageData.zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO;
        for(var i=0;i<trccfhdncpzzsjxseGridlbVO.length;i++){
          var ncphyl = trccfhdncpzzsjxseGridlbVO[i].ncphyl;
          if(ncphyl>1 ||ncphyl<0 ){
            return `<div style="color:red" class="errorDiv">0＜L3≤1。</div><br>`
          }else{
            return true;
          }
        }
      },
      // 0＜L1≤1
      L1Fun: function(){
        var trccfhdncpzzsjxseGridlbVO = this.tableData.pageData.zzsybnsrsb_cbfhdccpzzsjxsejsb.zzsybnsrsb_cbfhdccpzzsjxsejsbbwGridlbVO;
        for(var i=0;i<trccfhdncpzzsjxseGridlbVO.length;i++){
          var kcl = trccfhdncpzzsjxseGridlbVO[i].kcl;
          if(kcl>1 ||kcl<0 ){
            return `<div style="color:red" class="errorDiv">0＜L3≤1。</div><br>`
          }else{
            return true;
          }
        }
      }

    },
    provide() {
      return {
        resetData: this.resetData
      };
    }
  };
</script>

<style scoped>

</style>





