            }
          }
        }
      }

    },
    created:function () {
      this.initializationData = this.getSelectSbbInfo();
      this.inTableVerification();
      this.$route.params.id;
      //this.initializationData = this.getSelectSbbInfo(this.routerParam);
      this.tableData.pageData.checkData.kclList=this.getDict(this.jsonUrl+"/static/json/dict/houseAdvantage");
       this.initializationDataAssignment();  //初始化数据
      this.betweenTables();  //表间数据
      //this.initializationOfPublicServices()  //  暂时替换header节点
    }
  }
</script>
